import { handleActions } from "redux-actions";

const initialState = {
  CalculationResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const Calculation = handleActions(
  {
    Calculation_REQUEST: (state) => ({
      ...state,
      CalculationResponse: {
        ...state.defaultData,
      },
    }),
    Calculation_SUCCESS: (state, { payload }) => ({
      ...state,
      CalculationResponse: payload,
    }),
    Calculation_FAIL: (state, { payload }) => ({
      ...state,
      CalculationResponse: payload.data,
    }),
    Calculation_REQUEST_CLEAR: (state) => ({
      ...state,
      CalculationResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default Calculation;
