import React, { useEffect, useRef, useState, Component } from "react";
import { useDispatch, useSelector } from "react-redux";
import Footer from "../components/footer";
import nftpic from "../../assets/images/nft-pic.png";
import Web3 from "web3";
import erc20Abi from "../../metamask/erc20";

import GetAllCurrencyAction from "../../Redux/Actions/CurrencyAction/GetAllCurrencyAction";
import GetFavouriteNftAction from "../../Redux/Actions/NftActions/GetFavouriteNftAction";
import RemoveFavouriteNftAction from "../../Redux/Actions/NftActions/RemoveFavouriteNftAction";
import CalculationAction, {
  CalculationRequest,
} from "../../Redux/Actions/SwapAction/CalculationAction";

import SwapFromModal from "../components/swapPopUpModal/SwapFromModal";
import SwapToModal from "../components/swapPopUpModal/SwapToModal";
import {
  sendTransection,
  switchMetamask,
  sendToken,
  getTransactionReceipt,
} from "../../metamask";
import AddWhiteLabelAddressShortAction from "../../Redux/Actions/WhiteLabelAddressAction/AddWhiteLabelAddressShortAction";
import SwapCreateAction from "../../Redux/Actions/SwapAction/SwapCreateAction";
import GetWhiteLabelAddressByChainIDAction from "../../Redux/Actions/WhiteLabelAddressAction/GetWhiteLabelAddressByChainIDAction";
import { Alert } from "react-bootstrap";
import { PulseLoader } from "react-spinners";
import { toast, ToastContainer } from "react-toastify";
import SwapConfirmModel from "../components/SwapConfirmModel/SwapConfirmationModel";
import http from "../../Redux/Api/http";
import defaultImg from "../../assets/images/default.png";
import { useHistory } from "react-router-dom";
import axios from "axios";
import PupolarViewsNftAction from "../../Redux/Actions/PupolarViewsNftAction/PupolarViewsNftAction";

class CustomSlide extends Component {
  render() {
    const { index, ...props } = this.props;
    return <div {...props}></div>;
  }
}

const Swap = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const fromInputRef = useRef();
  const toInputRef = useRef();
  const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;

  const [openConfirmModel, setOpenConfirmModel] = useState(false);
  const [updatedBalance, setUpdatedBalance] = useState();
  const [fromPopModal, setFromPopModal] = useState(false);
  const [toPopModal, setToPopModal] = useState(false);
  const [availableBalanceError, setAvailableBalanceError] = useState(false);
  const [currencyCalculationData, setCurrencyCalculationData] = useState({});

  const [fromSelectedBlockChain, setFromSelectedBlockChain] = useState({});
  const [toSelectedBlockChain, setToSelectedBlockChain] = useState({});
  const [isLoading, setIsLoading] = useState(false);

  const [fromSelectedCurrency, setFromSelectedCurrency] = useState({});

  const [toSelectedCurrency, setToSelectedCurrency] = useState({});
  const [toWhiteLabelAddress, setToWhiteLabelAddress] = useState("");
  const [startMultiSwap, setStartMultiSwap] = useState(false);
  const [popularNft, setPopularNft] = useState([]);

  const handleConfirm = (e) => {
    e.preventDefault();
    setOpenConfirmModel(true);
  };

  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );

  const WalletAddresChainId = useSelector(
    (state) => state?.WalletConnction?.WalletResponse?.chainId
  );

  const PupolarViewsNft = useSelector(
    (state) => state?.PupolarViewsNft?.PupolarViewsNftResponse?.data
  );

  const isConnected = useSelector(
    (state) => state.Login?.authResponse?.data?.token
  );

  const balance = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.balance
  );

  const GetWhiteLabelAddressByChainID = useSelector(
    (state) =>
      state?.GetWhiteLabelAddressByChainID
        ?.GetWhiteLabelAddressByChainIDResponse?.data
  );

  const Token = useSelector((state) => state.Login?.authResponse?.data?.token);

  const GetFavouriteNft = useSelector(
    (state) => state.GetFavouriteNft?.GetFavouriteNftResponse?.data
  );

  useEffect(async () => {
    await dispatch(GetFavouriteNftAction())
      .then(() => {})
      .catch(() => {});
    await dispatch(PupolarViewsNftAction())
      .then((res) => {
        // console.log("popular Nfts", res.data);
        const PopNfts = res?.data;
        const PopularSortedNfts = PopNfts.sort((a, b) => {
          return b.viewCount - a.viewCount;
        });

        setPopularNft(PopularSortedNfts);
      })
      .catch((err) => {
        console.log("popular Nfts", err);
      });

    // http
    //   .get(httpUrl + "/api/v1/Nft/PupolarViewsNft?PageSize=2")
    //   .then(async (res) => {
    //     setPopularNft(res.data.data);
    //   })
    //   .catch((error) => {
    //     console.log(error?.message);
    //   });

    setTimeout(() => {
      setStartMultiSwap(true);
    }, 4000);
  }, []);

  const removeToFavourite = async (nftId, OwnerAddress) => {
    const payload = {
      nftId: nftId,
      nftAddress: OwnerAddress,
    };

    await dispatch(RemoveFavouriteNftAction(payload))
      .then(async (resp) => {
        if (resp?.isSuccess === true) {
          const updateFavouriteNumber = popularNft.map((item) => {
            if (nftId == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount - 1,
              };
            } else {
              return {
                ...item,
              };
            }
          });
          setPopularNft([...updateFavouriteNumber]);
          toast.error(`${resp?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          await dispatch(GetFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        // swal({
        //   icon: "error",
        //   title: error?.data?.message,
        // });
      });
  };

  const addToFavourite = async (nftID, OwnerAddress) => {
    if (!isConnected) {
      toast.error(`Please connect to wallet first`, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return;
    }

    const payload = {
      nftId: nftID,
      nftAddress: OwnerAddress,
    };
    await axios
      .post(
        httpUrl + "/api/v1/Nft/AddFavouriteNft",
        {
          nftId: nftID,
          nftAddress: OwnerAddress,
        },
        {
          headers: {
            Authorization: `Bearer ${Token}`,
          },
        }
      )
      .then(async (resp) => {
        if (resp?.data?.isSuccess === true) {
          const updateFavouriteNumber = popularNft.map((item) => {
            if (nftID == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount + 1,
              };
            } else {
              return {
                ...item,
              };
            }
          });
          setPopularNft([...updateFavouriteNumber]);
          toast.success(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });

          await dispatch(GetFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`NFT Already Liked`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        // swal({
        //   icon: "error",
        //   title: error?.data?.message,
        // });
      });
  };

  useEffect(async () => {
    if (startMultiSwap) {
      const fromSelectedCurrencyStorage = localStorage.getItem(
        "fromSelectedCurrency"
      );

      const toSelectedCurrencyStorage =
        localStorage.getItem("toSelectedCurrency");

      const fromSelectedBlockChainStorage = localStorage.getItem(
        "fromSelectedBlockChain"
      );

      const toSelectedBlockChainStorage = localStorage.getItem(
        "toSelectedBlockChain"
      );

      const currencyCalculationDataStorage = localStorage.getItem(
        "currencyCalculationData"
      );

      const toWhiteLabelAddressStorage = localStorage.getItem(
        "toWhiteLabelAddress"
      );

      if (
        fromSelectedCurrencyStorage &&
        toSelectedCurrencyStorage &&
        fromSelectedBlockChainStorage &&
        toSelectedBlockChainStorage &&
        currencyCalculationDataStorage &&
        toWhiteLabelAddressStorage
      ) {
        // ...item,
        // balance: convertedToWei.substr(0, 6),
        if (
          JSON.parse(fromSelectedCurrencyStorage).chainId ==
          parseInt(WalletAddresChainId)
        ) {
          var fromBalance;
          if (JSON.parse(fromSelectedCurrencyStorage).smartContractAddress) {
            fromBalance = await getTokenBalance(
              JSON.parse(fromSelectedCurrencyStorage)?.smartContractAddress,
              WalletAddress
            );
            setTimeout(() => {
              setFromSelectedCurrency({
                ...JSON.parse(fromSelectedCurrencyStorage),
                balance: fromBalance,
              });
            }, 1500);
          } else {
            fromBalance = Web3.utils.fromWei(String(balance || 0));
            setTimeout(() => {
              setFromSelectedCurrency({
                ...JSON.parse(fromSelectedCurrencyStorage),
                balance: fromBalance,
              });
            }, 2000);
          }
        } else {
          setFromSelectedCurrency(JSON.parse(fromSelectedCurrencyStorage));
        }

        if (
          JSON.parse(toSelectedCurrencyStorage).chainId ==
          parseInt(WalletAddresChainId)
        ) {
          var toBalance;
          if (JSON.parse(toSelectedCurrencyStorage).smartContractAddress) {
            toBalance = await getTokenBalance(
              JSON.parse(toSelectedCurrencyStorage)?.smartContractAddress,
              WalletAddress
            );
            setTimeout(() => {
              setToSelectedCurrency({
                ...JSON.parse(toSelectedCurrencyStorage),
                balance: toBalance,
              });
            }, 2000);
          } else {
            toBalance = Web3.utils.fromWei(String(balance || 0));
            setTimeout(() => {
              setToSelectedCurrency({
                ...JSON.parse(toSelectedCurrencyStorage),
                balance: toBalance,
              });
            }, 2000);
          }
        } else {
          setToSelectedCurrency(JSON.parse(toSelectedCurrencyStorage));
        }

        setFromSelectedBlockChain(JSON.parse(fromSelectedBlockChainStorage));
        setToSelectedBlockChain(JSON.parse(toSelectedBlockChainStorage));
        setCurrencyCalculationData(JSON.parse(currencyCalculationDataStorage));
        setToWhiteLabelAddress(JSON.parse(toWhiteLabelAddressStorage));
      }
    }
  }, [startMultiSwap]);

  useEffect(() => {
    const convertedBalance = Web3.utils.fromWei(String(balance || 0));
    setUpdatedBalance(convertedBalance);
  }, [balance]);

  useEffect(() => {
    const fromSelectedCurrencyStorage = localStorage.getItem(
      "fromSelectedCurrency"
    );
    setTimeout(() => {
      if (
        fromSelectedCurrencyStorage &&
        toSelectedCurrency &&
        Object.keys(toSelectedCurrency).length != 0 &&
        fromSelectedCurrency &&
        fromSelectedCurrency.balance &&
        currencyCalculationData &&
        toWhiteLabelAddress
      ) {
        StartSwapping();
      }
    }, 3000);
  }, [
    fromSelectedCurrency,
    toSelectedCurrency,
    currencyCalculationData,
    toWhiteLabelAddress,
  ]);

  useEffect(async () => {
    if (isConnected) {
      // setWhitelistLabel(WalletAddress);

      const whiteLablePayload = {
        Address: WalletAddress,
        BlockchainChainId: parseInt(window.ethereum.chainId),
      };
      setTimeout(async () => {
        await dispatch(AddWhiteLabelAddressShortAction(whiteLablePayload))
          .then(() => {})
          .catch((err) => {});

        await dispatch(GetWhiteLabelAddressByChainIDAction(chainId))
          .then((res) => {
            const toWhiteLabelAddressStorage = localStorage.getItem(
              "toWhiteLabelAddress"
            );
            if (!toWhiteLabelAddressStorage) {
              setToWhiteLabelAddress(res?.data[0]?.address);
            }
          })
          .catch(() => {});
      }, 2000);
      //have to chain after controlling the multi chain
      const chainId = parseInt(WalletAddresChainId);
    }
  }, [isConnected]);

  useEffect(() => {
    setToSelectedCurrency(toSelectedCurrency);
  }, [toSelectedCurrency]);

  useEffect(() => {
    setFromSelectedCurrency(fromSelectedCurrency);
  }, [fromSelectedCurrency]);

  const getTokenBalance = async (tokenAddress, accountAddr) => {
    if (tokenAddress && accountAddr) {
      const contract = new window.web3.eth.Contract(erc20Abi, tokenAddress);
      return await contract.methods
        .balanceOf(accountAddr)
        .call()
        .then(async (data) => {
          const balance = await window.web3.utils.fromWei(data, "ether");
          return balance;
          console.log(data);
        });
    }
  };

  const handleCalculation = async (e) => {
    const { value, target } = e.target;
    if (value == "") {
      setAvailableBalanceError(false);
    }
    const re = /^\s*(?=.*[1-9])\d*(?:\.\d{1,5})?\s*$/g;
    const payload = {
      toCurrencyUuid: toSelectedCurrency.uuid,
      fromCurrencyUuid: fromSelectedCurrency.uuid,
      fromAmount: value,
    };

    if (re.test(value)) {
      await dispatch(CalculationAction(payload))
        .then((res) => {
          console.log(res?.data);
          setCurrencyCalculationData(res?.data);
          if (res?.data?.fromAmount >= fromSelectedCurrency?.balance) {
            setAvailableBalanceError(true);
            toInputRef.current.value = "";
          } else {
            setAvailableBalanceError(false);
          }
        })
        .catch(() => {
          toInputRef.current.value = "";
          setCurrencyCalculationData({});
        });
    } else {
      toInputRef.current.value = "";
      setCurrencyCalculationData({});
    }
  };

  const StartSwapping = async (e) => {
    setIsLoading(true);
    if (e) {
      e.preventDefault();
    }
    console.log(toWhiteLabelAddress);

    if (fromSelectedCurrency.chainId != parseInt(WalletAddresChainId)) {
      localStorage.setItem(
        "fromSelectedCurrency",
        JSON.stringify(fromSelectedCurrency)
      );
      localStorage.setItem(
        "toSelectedCurrency",
        JSON.stringify(toSelectedCurrency)
      );

      localStorage.setItem(
        "fromSelectedBlockChain",
        JSON.stringify(fromSelectedBlockChain)
      );

      localStorage.setItem(
        "toSelectedBlockChain",
        JSON.stringify(toSelectedBlockChain)
      );

      localStorage.setItem(
        "currencyCalculationData",
        JSON.stringify(currencyCalculationData)
      );

      localStorage.setItem(
        "toWhiteLabelAddress",
        JSON.stringify(toWhiteLabelAddress)
      );

      await switchMetamask(fromSelectedCurrency?.chainId)
        .then(() => {})
        .catch((err) => {
          localStorage.removeItem("fromSelectedCurrency");
          localStorage.removeItem("toSelectedCurrency");
          localStorage.removeItem("fromSelectedBlockChain");
          localStorage.removeItem("toSelectedBlockChain");
          localStorage.removeItem("currencyCalculationData");
          localStorage.removeItem("toWhiteLabelAddress");
          setIsLoading(false);
          toast.error(`${err?.message}`, {
            position: "top-right",

            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: undefined,
          });
        });

      return;
    }
    const TransferAmount = parseInt(
      Web3.utils.toWei(String(currencyCalculationData?.fromAmount))
    ).toString(16);

    if (fromSelectedCurrency?.currencyType == "Token") {
      const payload = {
        tokenAddress: fromSelectedCurrency?.smartContractAddress,
        from: WalletAddress,
        to: currencyCalculationData?.swapDepositAddress,
        value: currencyCalculationData?.fromAmount,
      };

      await sendToken(payload)
        .then(async (transactionHash) => {
          const payload = {
            toCurrencyUuid: currencyCalculationData.toCurrencyUuid,
            fromCurrencyUuid: currencyCalculationData.fromCurrencyUuid,
            fromAmount: currencyCalculationData.fromAmount,
            fromAddress: WalletAddress,
            toAddress: toWhiteLabelAddress,
            fromAddressDepositTo: currencyCalculationData.swapDepositAddress,
            transactionHash: transactionHash,
          };

          await dispatch(SwapCreateAction(payload))
            .then((res) => {
              // setIsLoading(false);

              transactionReceipt(transactionHash);
              // .then((res) => {
              //   console.log("Send token TransactionReciept", res);
              // })
              // .catch((error) => {
              //   console.log("Send token TransactionReciept error", error);
              // });
            })
            .catch((error) => {
              resetStorageOnly();
              setIsLoading(false);
              toast.error(`${error?.message}`, {
                position: "top-right",

                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
              });
            });
        })
        .catch((error) => {
          resetStorageOnly();
          setIsLoading(false);
          toast.error(`Transaction is rejected`, {
            position: "top-right",

            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: undefined,
          });
        });
    } else if (fromSelectedCurrency?.currencyType == "Blockchain") {
      console.log({ toSelectedCurrency, fromSelectedCurrency });

      const payload = [
        {
          from: WalletAddress,
          to: currencyCalculationData?.swapDepositAddress,
          value: TransferAmount,
        },
      ];

      await dispatch(sendTransection(payload))
        .then(async (transactionHash) => {
          const payload = {
            toCurrencyUuid: currencyCalculationData.toCurrencyUuid,
            fromCurrencyUuid: currencyCalculationData.fromCurrencyUuid,
            fromAmount: currencyCalculationData.fromAmount,
            fromAddress: WalletAddress,
            toAddress: toWhiteLabelAddress,
            fromAddressDepositTo: currencyCalculationData.swapDepositAddress,
            transactionHash: transactionHash,
          };

          await dispatch(SwapCreateAction(payload))
            .then((res) => {
              // localStorage.clear();

              transactionReceipt(transactionHash);
              // .then((res) => {
              //   console.log("Send Transaction TransactionReciept", res);
              // })
              // .catch((error) => {
              //   console.log(
              //     "Send Transaction TransactionReciept error",
              //     error
              //   );
              // });
            })
            .catch((error) => {
              resetStorageOnly();
              setIsLoading(false);
              toast.error(`${error?.message}`, {
                position: "top-right",

                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
              });
            });
        })
        .catch((error) => {
          resetStorageOnly();
          setIsLoading(false);
          toast.error(`Transaction is rejected`, {
            position: "top-right",

            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: undefined,
          });
        });
    }
  };

  const transactionReceipt = (transectionHash) => {
    var transReceiptInt = setInterval(async () => {
      await getTransactionReceipt(transectionHash)
        .then((receipt) => {
          console.log("getTransactionReciept", receipt);
          if (receipt) {
            clearInterval(transReceiptInt);
            setIsLoading(false);

            toast.success(
              "Swap completed successfully. Your wallet will be updated shortly",
              {
                position: "top-right",

                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
              }
            );

            resetSomeState();
          }
        })
        .catch((err) => {
          console.log("getting err", err);
        });
    }, 2000);
  };

  const resetStorageOnly = () => {
    localStorage.removeItem("fromSelectedCurrency");
    localStorage.removeItem("toSelectedCurrency");
    localStorage.removeItem("fromSelectedBlockChain");
    localStorage.removeItem("toSelectedBlockChain");
    localStorage.removeItem("currencyCalculationData");
    localStorage.removeItem("toWhiteLabelAddress");
  };

  const resetSomeState = () => {
    localStorage.removeItem("fromSelectedCurrency");
    localStorage.removeItem("toSelectedCurrency");
    localStorage.removeItem("fromSelectedBlockChain");
    localStorage.removeItem("toSelectedBlockChain");
    localStorage.removeItem("currencyCalculationData");
    localStorage.removeItem("toWhiteLabelAddress");

    dispatch(CalculationRequest());
    setTimeout(() => {
      setToSelectedCurrency({});
      setFromSelectedCurrency({});
      setToSelectedBlockChain({});
      setFromSelectedBlockChain({});
      setCurrencyCalculationData({});

      fromInputRef.current.value = "";
      toInputRef.current.value = "";
    }, 500);
  };

  return (
    <>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <section className="container-fluid liquidity-swap height-box">
        <div className="row">
          <div className="container">
            <div className="row">
              <div className="col-lg-3 col-sm-12 col-md-4 col-12">
                <h2>Most Popular</h2>
                {console.log("Popular NFT", popularNft)}
                {popularNft?.map((item, index) => {
                  return (
                    index < 2 && (
                      <>
                        <div
                          key={index}
                          className="d-item col-lg-12 col-md-12 col-sm-12 col-xs-12"
                        >
                          <div className="nft market-nft">
                            <CustomSlide className="itm" index={1}>
                              <div className="nft_coll">
                                <div className="nft_wrap">
                                  <span
                                    className=" pic-demo"
                                    onClick={() => {
                                      history.push(
                                        `/usernftdetail/${item?.id}/${item?.accountId}`
                                      );
                                    }}
                                    style={{
                                      background: `url(${
                                        httpUrl +
                                        "/" +
                                        item?.image?.replaceAll("\\", "/")
                                      }) no-repeat`,
                                    }}
                                  >
                                    <img
                                      src={httpUrl + "/" + item?.image}
                                      className="lazy popular-img"
                                      alt=""
                                    />
                                  </span>
                                </div>
                                <div className="nft_coll_pp">
                                  <span
                                    onClick={() => {
                                      history.push(
                                        item.ownerAddress === WalletAddress
                                          ? `/myprofile`
                                          : `/profile/${item.ownerAddress}`
                                      );
                                    }}
                                  >
                                    <img
                                      className="lazy"
                                      src={
                                        item?.ownerImage
                                          ? httpUrl + "/" + item?.ownerImage
                                          : defaultImg
                                      }
                                      alt=""
                                    />
                                  </span>
                                </div>
                                <div className="nft_coll_info">
                                  <span
                                    onClick={() => {
                                      history.push(
                                        `/usernftdetail/${item?.id}/${item?.accountId}`
                                      );
                                    }}
                                  >
                                    {item?.name?.substring().length > 20 ? (
                                      <h4>
                                        {" "}
                                        {`${item?.name.substring(0, 20)}...`}
                                      </h4>
                                    ) : (
                                      <>
                                        <h4>{item?.name}</h4>
                                      </>
                                    )}
                                  </span>
                                  <span
                                    onClick={() => {
                                      history.push(
                                        `/usernftdetail/${item?.id}/${item?.accountId}`
                                      );
                                    }}
                                  >
                                    Price {" " + item?.buyPrice + " "}BNB
                                  </span>
                                  <div class="full-div">
                                    <a
                                      onClick={() => {
                                        history.push(
                                          `/usernftdetail/${item?.id}/${item?.accountId}`
                                        );
                                      }}
                                      class="view-all-btn"
                                    >
                                      Detail{" "}
                                      <i
                                        class="fa fa-angle-right"
                                        aria-hidden="true"
                                      ></i>
                                    </a>
                                    {GetFavouriteNft?.some((itm, index) => {
                                      return itm.nftTokenId === item.nftTokenId;
                                    }) ? (
                                      <i
                                        onClick={() => {
                                          removeToFavourite(
                                            item.id,
                                            item.ownerAddress
                                          );
                                        }}
                                        className="fa fa-heart"
                                        style={{ color: "red" }}
                                      >
                                        {" "}
                                        {item?.nftFavouritesCount}
                                      </i>
                                    ) : (
                                      <i
                                        onClick={() => {
                                          addToFavourite(
                                            item?.id,
                                            item.ownerAddress
                                          );
                                        }}
                                        className="fa fa-heart"
                                      >
                                        {" "}
                                        {item?.nftFavouritesCount}
                                      </i>
                                    )}
                                  </div>
                                </div>
                              </div>
                            </CustomSlide>
                          </div>
                        </div>
                        <div className="full-div text-center">
                          <div className="border-div"></div>
                        </div>
                      </>
                    )
                  );
                })}
              </div>

              <div className="col-lg-9 col-sm-12 col-md-8 col-12">
                <div className="liquidity-swap-pnl">
                  <div className="text-pnl">
                    <h1>Swap</h1>
                    {availableBalanceError && (
                      <Alert variant={"danger"}>
                        Entered Amount is more than available balance
                      </Alert>
                    )}

                    <form>
                      <div className="row">
                        <div className="col-lg-12 col-sm-12 col-md-12 col-12">
                          <div className="full-div swap-input-pnl">
                            <div className="w-100">
                              <label>From</label>
                              <span className="float-right">
                                {fromSelectedCurrency?.balance
                                  ? `Balance: ${parseFloat(
                                      fromSelectedCurrency?.balance
                                    ).toFixed(3)}`
                                  : fromSelectedCurrency?.chainId ==
                                    parseInt(window.ethereum.chainId)
                                  ? `Balance:${parseFloat(
                                      updatedBalance
                                    ).toFixed(3)}`
                                  : ""}
                              </span>
                            </div>

                            <div className="row mb-2">
                              <div className="col-lg-6 col-sm-12 col-md-6 col-12">
                                <input
                                  type="number"
                                  name="liquidity-input"
                                  id="liquidity-input"
                                  ref={fromInputRef}
                                  defaultValue={
                                    currencyCalculationData?.fromAmount
                                  }
                                  className="form-control"
                                  placeholder="0.0"
                                  disabled={
                                    !(
                                      Object.keys(fromSelectedCurrency)
                                        .length != 0 &&
                                      Object.keys(toSelectedCurrency)?.length !=
                                        0
                                    )
                                  }
                                  onChange={(e) => {
                                    handleCalculation(e);
                                  }}
                                />
                              </div>
                              <div className="col-lg-4 offset-lg-2 col-sm-12 col-md-6 col-12 mt-sm-1">
                                <div
                                  className="select_currency_swap p-2"
                                  onClick={() => setFromPopModal(true)}
                                >
                                  <span>
                                    {Object.keys(fromSelectedCurrency)
                                      ?.length === 0
                                      ? "Select Currency"
                                      : fromSelectedCurrency?.name}
                                  </span>
                                </div>
                                <SwapFromModal
                                  showModal={fromPopModal}
                                  hideModal={setFromPopModal}
                                  setFromSelectedBlockChain={
                                    setFromSelectedBlockChain
                                  }
                                  fromSelectedBlockChain={
                                    fromSelectedBlockChain
                                  }
                                  setFromSelectedCurrency={
                                    setFromSelectedCurrency
                                  }
                                  fromSelectedCurrency={fromSelectedCurrency}
                                  toSelectedCurrency={toSelectedCurrency}
                                  toInputRef={toInputRef}
                                  fromInputRef={fromInputRef}
                                  setCurrencyCalculationData={
                                    setCurrencyCalculationData
                                  }
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-lg-12 col-sm-12 col-md-12 col-12">
                          <div className="full-div swap-input-pnl">
                            <label>To</label>
                            <span className="float-right">
                              {toSelectedCurrency?.balance
                                ? `Balance: ${parseFloat(
                                    toSelectedCurrency?.balance
                                  ).toFixed(3)}`
                                : toSelectedCurrency?.chainId ==
                                  parseInt(window.ethereum.chainId)
                                ? `Balance:${parseFloat(updatedBalance).toFixed(
                                    3
                                  )}`
                                : ""}
                            </span>
                            <div className="row mb-2">
                              <div className="col-lg-6 col-sm-12 col-md-6 col-12">
                                <input
                                  type="number"
                                  ref={toInputRef}
                                  name="liquidity-input"
                                  id="liquidity-input"
                                  value={currencyCalculationData?.toAmount}
                                  className="form-control"
                                  placeholder="0.0"
                                  disabled
                                />
                              </div>
                              <div className="col-lg-4 offset-lg-2 col-sm-12 col-md-6 col-12 mt-sm-1">
                                <div
                                  className="select_currency_swap p-2"
                                  onClick={() => setToPopModal(true)}
                                >
                                  <span>
                                    {Object.keys(toSelectedCurrency)?.length ===
                                    0
                                      ? "Select Currency"
                                      : toSelectedCurrency.name}
                                  </span>
                                </div>
                                <SwapToModal
                                  showModal={toPopModal}
                                  hideModal={setToPopModal}
                                  setToSelectedBlockChain={
                                    setToSelectedBlockChain
                                  }
                                  toSelectedBlockChain={toSelectedBlockChain}
                                  setToSelectedCurrency={setToSelectedCurrency}
                                  toSelectedCurrency={toSelectedCurrency}
                                  fromSelectedCurrency={fromSelectedCurrency}
                                  toInputRef={toInputRef}
                                  fromInputRef={fromInputRef}
                                  setCurrencyCalculationData={
                                    setCurrencyCalculationData
                                  }
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-lg-12 col-sm-12 col-md-12 col-12">
                          <div className="full-div swap-input-pnl">
                            <div className="row">
                              <div className="col-lg-6 col-sm-12 col-md-6 col-12">
                                <label>Favourite Address</label>
                              </div>
                              <div className="col-lg-4 offset-lg-2 col-sm-12 col-md-6 col-12 ">
                                {/* <div class="dropdown liquidity-dropdown"> */}
                                {/* <button
                                    class="btn btn-secondary dropdown-toggle"
                                    type="button"
                                    id="dropdownMenuButton"
                                    data-toggle="dropdown"
                                    aria-haspopup="true"
                                    aria-expanded="false"
                                  >
                                    Select
                                  </button> */}
                                <select
                                  className="form-control transprant-input fav_add"
                                  onChange={(e) => {
                                    const { value, name } = e.target;

                                    setToWhiteLabelAddress(value);
                                  }}
                                >
                                  <option value="" style={{ display: "none" }}>
                                    Select Wallet Address
                                  </option>
                                  {GetWhiteLabelAddressByChainID?.map(
                                    (item, index) => {
                                      return (
                                        <option
                                          key={index}
                                          value={item?.address}
                                          selected={toWhiteLabelAddress}
                                        >
                                          {item.address}
                                        </option>
                                      );
                                    }
                                  )}
                                </select>
                                {/* </div> */}
                              </div>
                              <div className="col-lg-12col-sm-12 col-md-12 col-12">
                                {/* <input
                                  type="text"
                                  name="liquidity-input"
                                  id="liquidity-input"
                                  className="form-control"
                                  placeholder="0.0"
                                /> */}
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-lg-12 col-sm-12 col-md-12 col-12">
                          {!(
                            currencyCalculationData &&
                            currencyCalculationData.toAmount > 0 &&
                            Object.keys(currencyCalculationData)?.length != 0 &&
                            !availableBalanceError
                          ) ? (
                            <button className=" reg-btn disable-btn" disabled>
                              Swap
                            </button>
                          ) : isLoading ? (
                            <button className="reg-btn">
                              <PulseLoader color="white" size="11" />
                            </button>
                          ) : (
                            <button
                              className="reg-btn"
                              onClick={(e) => {
                                // StartSwapping(e);
                                handleConfirm(e);
                              }}
                            >
                              Swap
                            </button>
                          )}
                          <SwapConfirmModel
                            showModal={openConfirmModel}
                            hideModal={setOpenConfirmModel}
                            data={currencyCalculationData}
                            action={StartSwapping}
                            fromSelectedCurrency={fromSelectedCurrency}
                            toSelectedCurrency={toSelectedCurrency}
                          />
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};
export default Swap;
