import { handleActions } from "redux-actions";

const initialState = {
  DeleteWhiteLabelAddressResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const DeleteWhiteLabelAddress = handleActions(
  {
    DeleteWhiteLabelAddress_REQUEST: (state) => ({
      ...state,
      DeleteWhiteLabelAddressResponse: {
        ...state.defaultData,
      },
    }),
    DeleteWhiteLabelAddress_SUCCESS: (state, { payload }) => ({
      ...state,
      DeleteWhiteLabelAddressResponse: payload,
    }),
    DeleteWhiteLabelAddress_FAIL: (state, { payload }) => ({
      ...state,
      DeleteWhiteLabelAddressResponse: payload.data,
    }),
    DeleteWhiteLabelAddress_REQUEST_CLEAR: (state) => ({
      ...state,
      DeleteWhiteLabelAddressResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default DeleteWhiteLabelAddress;
