import { handleActions } from "redux-actions";

const initialState = {
  LiquidityProvidersHistoryResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const LiquidityProvidersHistory = handleActions(
  {
    LiquidityProvidersHistory_REQUEST: (state) => ({
      ...state,
      LiquidityProvidersHistoryResponse: {
        ...state.defaultData,
      },
    }),
    LiquidityProvidersHistory_SUCCESS: (state, { payload }) => ({
      ...state,
      LiquidityProvidersHistoryResponse: payload,
    }),
    LiquidityProvidersHistory_FAIL: (state, { payload }) => ({
      ...state,
      LiquidityProvidersHistoryResponse: payload.data,
    }),
    LiquidityProvidersHistory_REQUEST_CLEAR: (state) => ({
      ...state,
      LiquidityProvidersHistoryResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default LiquidityProvidersHistory;
