import { handleActions } from "redux-actions";

const initialState = {
  GetUserFavouriteNftResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const GetUserFavouriteNft = handleActions(
  {
    GetUserFavouriteNft_REQUEST: (state) => ({
      ...state,
      GetUserFavouriteNftResponse: {
        ...state.defaultData,
      },
    }),
    GetUserFavouriteNft_SUCCESS: (state, { payload }) => ({
      ...state,
      GetUserFavouriteNftResponse: payload,
    }),
    GetUserFavouriteNft_FAIL: (state, { payload }) => ({
      ...state,
      GetUserFavouriteNftResponse: payload.data,
    }),
    GetUserFavouriteNft_REQUEST_CLEAR: (state) => ({
      ...state,
      GetUserFavouriteNftResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default GetUserFavouriteNft;
