import React, { useEffect, useState, useReducer } from "react";
import { useHistory } from "react-router-dom";
import MyNft from "./MyNfts/MyNfts";
import Footer from "../components/footer";
import { createGlobalStyle } from "styled-components";
import { Link, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { FaGlobe, FaInstagram, FaTwitter } from "react-icons/fa";
import { FaUserCircle } from "react-icons/fa";
import { ToastContainer, toast } from "react-toastify";
import MyCollections from "./MyCollections";
import GetMyAllCollectionsAction from "../../Redux/Actions/CollectionAction/GetMyAllCollections";
import AllFavourite from "./AllFavorite/AllFavourite";
import { WalletDisconnect } from "../../Redux/Actions/WalletActions/WalletAction";
import { AuthConnectRequest } from "../../Redux/Actions/AuthActions/AuthConnectAction";
import { LogoutAction } from "../../Redux/Actions/AuthActions/LogoutAction";
import { ValidateSignatureRequest } from "../../Redux/Actions/AuthActions/ValidateSignatureAction";
import { Dropdown, DropdownButton } from "react-bootstrap";
import http from "../../Redux/Api/http";
import GetNftCollectionByIdWithOutAccountAction from "../../Redux/Actions/NftActions/GetNftCollectionByIdWithOutAccountAction";


const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;


const initialState = { isDisable: false };

const reducer = (state, action) => {
  switch (action.type) {
    case 'clicked':
      return { isDisable: true };
    case 'notClicked':
      return { isDisable: false };
  }
}


const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    background: #212428;
  }
`;

const changeImage = (event) => {
  var image = document.getElementById("output");
  image.src = URL.createObjectURL(event.target.files[0]);
};

const MyProfile = function () {

  const [route, setRoute] = useState("");
  const [showMore, setShowMore] = useState(false);
  const [ProfileData, setprofiledata] = useState(false);

  const history = useHistory();


  const [state, disableDispatch] = useReducer(reducer, initialState)

  const location = useLocation()

  const GetNftCollectionByIdWithOutAccount = useSelector(
    (state) =>
      state?.GetNftCollectionByIdWithOutAccount
        ?.GetNftCollectionByIdWithOutAccountResponse?.data
  );

  useEffect(() => {
    if (location.state?.center) {
      handleBtnClick1()
    }
  }, [location.state])


  const dispatch = useDispatch();
  const User = useSelector((state) => state.Login);

  const Logoutt = async () => {
    await dispatch(WalletDisconnect());
    await dispatch(AuthConnectRequest());
    await dispatch(LogoutAction());
    await dispatch(ValidateSignatureRequest());
  };

  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );

  const MyProfile = useSelector(
    (state) => state.MyProfile?.MyProfileResponse?.data
  );

  useEffect(() => {

    setprofiledata(MyProfile)

  }, [MyProfile])



  const [openMenu, setOpenMenu] = React.useState(true);
  const [openMenu1, setOpenMenu1] = React.useState(false);
  const [openMenu2, setOpenMenu2] = React.useState(false);
  const [openMenu3, setOpenMenu3] = React.useState(false);
  const handleBtnClick = () => {
    setOpenMenu(true);
    setOpenMenu1(false);
    setOpenMenu2(false);
    setOpenMenu3(false);

    document.getElementById("Mainbtn").classList.add("active");
    document.getElementById("Mainbtn1").classList.remove("active");
    document.getElementById("Mainbtn2").classList.remove("active");
  };
  const handleBtnClick1 = () => {
    setOpenMenu1(true);
    setOpenMenu2(false);
    setOpenMenu(false);
    setOpenMenu3(false);

    document.getElementById("Mainbtn1").classList.add("active");
    document.getElementById("Mainbtn").classList.remove("active");
    document.getElementById("Mainbtn2").classList.remove("active");
  };
  const handleBtnClick2 = () => {
    setOpenMenu2(true);
    setOpenMenu(false);
    setOpenMenu1(false);
    setOpenMenu3(false);

    document.getElementById("Mainbtn").classList.remove("active");
    document.getElementById("Mainbtn1").classList.remove("active");
    document.getElementById("Mainbtn2").classList.add("active");
  };
  const handleBtnClick3 = () => {
    setOpenMenu2(false);
    setOpenMenu(false);
    setOpenMenu1(false);
    setOpenMenu3(true);
    document.getElementById("Mainbtn").classList.remove("active");
    document.getElementById("Mainbtn1").classList.remove("active");
    document.getElementById("Mainbtn2").classList.remove("active");
  };
  const text = MyProfile?.bio?.toString();

  return (
    <div>
      <GlobalStyles />
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <section className="container-fluid myprofile-container height-box">
        <div className="row">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <div class="my-profile-information">
                  <div className="img-pnl">
                    {ProfileData?.profileImage ? (
                      <div
                        style={{
                          width: 400,
                          height: "auto",
                          borderRadius: "100%",
                        }}
                      >
                        <img
                          src={httpUrl + "/" + ProfileData?.profileImage}
                          alt="profile.png"
                          style={{
                            width: 400,
                            height: 400,
                            objectFit: "cover",
                          }}
                        />
                      </div>
                    ) : (
                      <div
                        style={{
                          width: 400,
                          height: "auto",
                          borderRadius: "100%",
                        }}
                      >
                        <FaUserCircle size="2x" />
                      </div>
                    )}
                  </div>
                  <div className="txt-pnl">
                    <div className="profile_name">

                      <h4 className="heloo-heading pr">

                        <ul style={{ float: "none", marginTop: "0px" }} className="share-info-list">
                          <li >
                            {ProfileData?.username ? ProfileData?.username : "Unnamed"}
                          </li>
                          <li>
                            <DropdownButton
                              id="dropdown-basic-button"
                              className="social--btn fa fa-share-alt"
                            >
                              {!ProfileData?.yourSiteLink &&
                                !ProfileData?.twitterLink &&
                                !ProfileData?.instagramLink && (
                                  <> <span className="text-center collection-social">No links found </span></>
                                )}
                                {console.log("MyProfile Profile Data", ProfileData)}
                              {ProfileData?.yourSiteLink && (
                                <Dropdown.Item title="website" onClick={() => { window.open(ProfileData?.yourSiteLink) }}><i class="fa fa-globe web-color"  ></i> Website</Dropdown.Item>
                              ) }

                              {ProfileData?.twitterLink && (
                                <Dropdown.Item title="twitter" onClick={() => { window.open(ProfileData?.twitterLink) }}> <i className="fa fa-twitter twitter-color"></i> Twitter </Dropdown.Item>
                              )}

                              {ProfileData?.instagramLink && (
                                <Dropdown.Item title="instagram" onClick={() => { window.open(ProfileData?.instagramLink) }} ><i className="fa fa-instagram insta-color"></i> Instagram</Dropdown.Item>
                              ) }

                            </DropdownButton>
                          </li>
                          <span className="profile_username">
                          </span>
                        </ul>

                        <div style={{display:"flow-root"}}>
                          <span id="wallet" className="profile_wallet hover-blue">
                            {console.log("MyProfile WalletAddress", WalletAddress)}
                            {WalletAddress}{" "}
                          </span>

                          <CopyToClipboard
                            text={WalletAddress}
                            onCopy={() => {

                              disableDispatch({ type: 'clicked' })

                              toast.success("Address copied successfully", {
                                position: "top-right",
                                autoClose: 5000,
                                hideProgressBar: false,
                                closeOnClick: true,
                                pauseOnHover: false,
                                draggable: true,
                                progress: undefined,
                              });

                              setTimeout(() => {
                                disableDispatch({ type: 'notClicked' })
                              }, 5000);
                            }}
                          >
                            <button
                              id="btn_copy"
                              title="Copy Address"
                              disabled={state.isDisable}
                            >
                              Copy
                            </button>
                          </CopyToClipboard>
                        </div>
                      </h4>
                      <span className="join-span" style={{ wordWrap: 'break-word' }}>{ProfileData?.email}  </span>
                      <>
                        <span className="join-span " style={{ wordWrap: 'break-word' }}>
                          {!text? ("") : !showMore ? `${text?.substring(0, 68)}` : text}

                          {text?.length > 68 ? (
                            <span className="btn-more-less contract-hover" onClick={() => setShowMore(!showMore)}>
                              <br />
                              {showMore ? " show less" : "...show more"}
                            </span>
                          ) : ""
                          }
                        </span>
                      </>
                    </div>

                    <div className="full-div">
                      <div className="spacer-30"></div>
                      {openMenu && (
                        <Link className="sell-item-btn" to="/addcollection">
                          Add Collection
                        </Link>
                      )}

                      {openMenu1 && (
                        <Link className="sell-item-btn" to="/createnft">
                          Create NFT
                        </Link>
                      )}

                    </div>
                    <div className="full-div">
                      <div className="items_filter">
                        <ul className="de_nav de_nav">
                          <li id="Mainbtn" className="active">
                            <span onClick={handleBtnClick}>Collection</span>
                          </li>
                          <li id="Mainbtn1" className="">
                            <span onClick={handleBtnClick1}>My NFTs</span>
                          </li>
                          <li id="Mainbtn2" className="">
                            <span onClick={handleBtnClick2}>Favourites</span>
                          </li>

                        </ul>
                      </div>
                    </div>
                    <div className="spacer-30"></div>
                  </div>
                </div>
              </div>


              {openMenu && (

                <div id="zero1" className="onStep fadeIn">

                  <MyCollections />
                </div>
              )}
              {openMenu1 && (
                <div id="zero2" className="onStep fadeIn">
                  <MyNft />
                </div>
              )}
              {openMenu2 && (
                <div id="zero3" className="onStep fadeIn">
                  <AllFavourite />
                </div>
              )}
              {openMenu3 && (
                <div id="zero3" className="onStep fadeIn">
                  <MyNft />{" "}
                </div>
              )}
            </div>
          </div>
        </div>
      </section>




      <Footer />
    </div>
  );
};
export default MyProfile;
