import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const GetAllMasterWalletByCurrencyIdRequest = createAction(
  "GetAllMasterWalletByCurrencyId_REQUEST"
);
export const GetAllMasterWalletByCurrencyIdSuccess = createAction(
  "GetAllMasterWalletByCurrencyId_SUCCESS"
);
export const GetAllMasterWalletByCurrencyIdFail = createAction(
  "GetAllMasterWalletByCurrencyId_FAIL"
);

const GetAllMasterWalletByCurrencyIdAction = (body) => (dispatch) => {
  dispatch(GetAllMasterWalletByCurrencyIdRequest());
  return new Promise((resolve, reject) => {
    return Api.GetAllMasterWalletByCurrencyId.GetAllMasterWalletByCurrencyIdApi(
      body
    )
      .then(({ data }) => {
        dispatch(GetAllMasterWalletByCurrencyIdSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(GetAllMasterWalletByCurrencyIdFail(error));
        reject(error);
      });
  });
};
export default GetAllMasterWalletByCurrencyIdAction;
