import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const RefundTransactionRequest = createAction(
  "RefundTransaction_REQUEST"
);
export const RefundTransactionSuccess = createAction(
  "RefundTransaction_SUCCESS"
);
export const RefundTransactionFail = createAction("RefundTransaction_FAIL");

const RefundTransactionAction = (body) => (dispatch) => {
  // alert(body);
  dispatch(RefundTransactionRequest());
  return new Promise((resolve) => {
    return Api.RefundTransaction.RefundTransactionApi(body)
      .then(({ data }) => {
        dispatch(RefundTransactionSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(RefundTransactionFail(error));
        resolve();
      });
  });
};
export default RefundTransactionAction;
