import { handleActions } from "redux-actions";

const initialState = {
  AddLiquidityResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const AddLiquidity = handleActions(
  {
    AddLiquidity_REQUEST: (state) => ({
      ...state,
      AddLiquidityResponse: {
        ...state.defaultData,
      },
    }),
    AddLiquidity_SUCCESS: (state, { payload }) => ({
      ...state,
      AddLiquidityResponse: payload,
    }),
    AddLiquidity_FAIL: (state, { payload }) => ({
      ...state,
      AddLiquidityResponse: payload.data,
    }),
    AddLiquidity_REQUEST_CLEAR: (state) => ({
      ...state,
      AddLiquidityResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default AddLiquidity;
