import { handleActions } from "redux-actions";

const initialState = {
  RefundTransactionResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const RefundTransaction = handleActions(
  {
    RefundTransaction_REQUEST: (state) => ({
      ...state,
      RefundTransactionResponse: {
        ...state.defaultData,
      },
    }),
    RefundTransaction_SUCCESS: (state, { payload }) => ({
      ...state,
      RefundTransactionResponse: payload,
    }),
    RefundTransaction_FAIL: (state, { payload }) => ({
      ...state,
      RefundTransactionResponse: payload.data,
    }),
    RefundTransaction_REQUEST_CLEAR: (state) => ({
      ...state,
      RefundTransactionResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default RefundTransaction;
