import React, { useEffect, useState } from "react";
import { Button, Modal, Row, Col, H3 } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// import {
//   faArrowDown,
//   faQuestionCircle,
//   faSyncAlt,
// } from "@fortawesome/free-solid-svg-icons";

export default function LiquidityConfirmModel(props) {
  const handleOnclick = () => {
    props.hideModal(false);
    props.action();
  };

  const handleClose = () => {
    props.hideModal(false);
  };

  return (
    <>
      <Modal
        show={props.showModal}
        onHide={handleClose}
        // backdrop='static'
        // keyboard={false}
      >
        <Modal.Header className={"border-0 p-4"} closeButton>
          <Modal.Title className={"text-dark"}>Confirm Pool</Modal.Title>
        </Modal.Header>
        <Modal.Body className={"ps-4 pt-0 pb-0 pe-4"}>
          <Row className="flex-column">
            <Row className="justify-content-between">
              <Col xs={5}>
                <span>
                  <strong>First Currency: </strong>
                </span>
              </Col>
              <Col xs={5}>
                <span>
                  {props.firstSelectedCurrency?.image && (
                    <img
                      src={props.firstSelectedCurrency?.image}
                      width={30}
                      height={30}
                    />
                  )}{" "}
                  {props.data?.fromCurrencyName}
                </span>
              </Col>
            </Row>
            <Row className="justify-content-between">
              <Col xs={5}>
                <span>
                  <strong>First Amount: </strong>
                </span>
              </Col>
              <Col xs={5}>
                <span style={{ marginLeft: 35 }}></span>{" "}
                <span>{props.data?.fromAmount}</span>
              </Col>
            </Row>
            <Row className="justify-content-between">
              <Col xs={5}>
                <span>
                  <strong>Second Currency:</strong>
                </span>
              </Col>

              <Col xs={5}>
                <span>
                  {props.secondSelectedCurrency?.image && (
                    <img
                      src={props.secondSelectedCurrency?.image}
                      width={30}
                      height={30}
                    />
                  )}{" "}
                  {props.data?.toCurrencyName}
                </span>
              </Col>
            </Row>
            <Row className="justify-content-between">
              <Col xs={5}>
                <span>
                  <strong>Second Amount: </strong>
                </span>
              </Col>
              <Col xs={5}>
                <span style={{ wordBreak: 'break-all' }}></span>{" "}
                <span> {props.data?.toAmount?.toFixed(14)}</span>
              </Col>
            </Row>
          </Row>
        </Modal.Body>
        <Modal.Footer className="flex-column border-0 p-4">
          <Button
            variant="primary w-100"
            // className={}
            size="lg"
            onClick={handleOnclick}
          >
            Confirm Pool
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
