import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const AddWhiteLabelAddressRequest = createAction(
  "AddWhiteLabelAddress_REQUEST"
);
export const AddWhiteLabelAddressSuccess = createAction(
  "AddWhiteLabelAddress_SUCCESS"
);
export const AddWhiteLabelAddressFail = createAction(
  "AddWhiteLabelAddress_FAIL"
);

const AddWhiteLabelAddressAction = (body) => (dispatch) => {
  dispatch(AddWhiteLabelAddressRequest());
  return new Promise((resolve, reject) => {
    return Api.AddWhiteLabelAddress.AddWhiteLabelAddressApi(body)
      .then(({ data }) => {
        dispatch(AddWhiteLabelAddressSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(AddWhiteLabelAddressFail(error));
        reject(error);
      });
  });
};
export default AddWhiteLabelAddressAction;
