import React, { useState, useEffect, Component } from "react";
import { Link, useHistory } from "react-router-dom";
import BuyUserNft from "./BuyUserNft";
import http from "../../../Redux/Api/http";
import { PulseLoader } from "react-spinners";
import bnbimg from "../../../assets/images/bnb.png";
import { useDispatch, useSelector } from "react-redux";
import { toast, ToastContainer } from "react-toastify";
import swal from "sweetalert";
import axios from "axios";
import RemoveFavouriteNftAction from "../../../Redux/Actions/NftActions/RemoveFavouriteNftAction";
import GetFavouriteNftAction from "../../../Redux/Actions/NftActions/GetFavouriteNftAction";
import defaultImg from "../../../assets/images/default.png";



class CustomSlide extends Component {
  render() {
    const { index, ...props } = this.props;
    return <div {...props}></div>;
  }
}

function OnSaleUserNfts() {
  const [userNftData, setUserNftData] = useState();
  const [GetFavouriteNft, setGetFavouriteNft] = useState();
  const [loading, setLoading] = useState(true);
  const [numItems, setNumItems] = useState(4)
  const history = useHistory();
  const [height, Setheight] = useState(270);
  const [nftId, setNftId] = useState();
  // const [collectionData, setCollectionData] = useState();
  // const [favouriteInProgress, setFavouriteInProgress] = useState(false);
  // const [userAddress, setUserAddress] = useState();
  // const [nfts, Setnfts] = useState([userNftData?.slice(0, 4)]);
  
  const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;


  const dispatch = useDispatch();
  
  const Token = useSelector((state) => state.Login?.authResponse?.data?.token);

  const isConnected = useSelector(
    (state) => state.Login?.authResponse?.data?.token
  );

  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );

  useEffect(async () => {

    await http
      .get(
        httpUrl +
        `/api/v1/Nft/GetMyFavouriteNft`
      )
      .then((res) => {
        setGetFavouriteNft(res.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [userNftData])

  useEffect(async () => {
    let params = window.location.pathname;

    await http
      .get(
        httpUrl +
        `/api/v1/Account/GetUserAccount?address=${params.split("/")[2]}`
      )
      .then((res) => {
        setUserNftData(res.data.data.nftRequestModelList);
        setLoading(false);
      })
      .catch((error) => {
        console.log("APi", error);
      });
  }, [userNftData]);


  const addToFavourite = async (nftID, OwnerAddress) => {
    if (!isConnected) {
      toast.error(`Please connect to wallet first`, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return;
    }

    await axios
      .post(
        httpUrl + "/api/v1/Nft/AddFavouriteNft",
        {
          nftId: nftID,
          nftAddress: OwnerAddress,
        },
        {
          headers: {
            Authorization: `Bearer ${Token}`,
          },
        }
      )
      .then(async (resp) => {
        if (resp?.data?.isSuccess === true) {
          const updateFavouriteNumber = userNftData.map((item) => {
            if (nftId == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount + 1,

              };
            } else {
              return {
                ...item,
              };
            }
          });
          setUserNftData([...updateFavouriteNumber]);
          toast.success(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });

          await dispatch(GetFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`NFT Already Liked`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        // swal({
        //   icon: "error",
        //   title: error?.data?.message,
        // });
      });
  };

  const removeToFavourite = async (nftId, OwnerAddress) => {
    const payload = {
      nftId: nftId,
      nftAddress: OwnerAddress,
    };

    await dispatch(RemoveFavouriteNftAction(payload))
      .then(async (resp) => {
        if (resp?.isSuccess === true) {
          const updateFavouriteNumber = userNftData.map((item) => {
            if (nftId == item.id) {

              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount - 1,

              };
            } else {
              return {
                ...item,
              };
            }
          });
          setUserNftData([...updateFavouriteNumber]);
          toast.error(`${resp?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          await dispatch(GetFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        // swal({
        //   icon: "error",
        //   title: error?.data?.message,
        // });
      });
  };

  const loadMore = () => {
    if (userNftData?.length > numItems) {
      setNumItems((prev) => prev + 4)
    }
    // console.log("useer Data", userNftData)
    // let nftState = nfts;
    // let start = nftState.length;
    // let end = nftState.length + 4;
    // Setnfts([...nftState, ...userNftData?.slice(start, end)]);
  };

  const onImgLoad = ({ target: img }) => {
    let currentHeight = height;
    if (currentHeight < img.offsetHeight) {
      Setheight({
        height: img.offsetHeight,
      });
    }
  };


  return (
    <div className="row">
      {loading ? (
        <PulseLoader color="white" size="11" />
      ) : (
        <>
          {/* {console.log("user created nfts", nfts.length)} */}
          {/* //workhere */}
          {userNftData?.length == 0 ? (
            <div className="col-sm-12 text-center">
              No NFT on sale
            </div>
          ) : (
            ""
          )}
          {userNftData.slice(0, numItems).map((item, index) => (
            <>
              {item.staus === 'ReadyForSell' && (
                <div
                  key={index}
                  className="d-item col-lg-3 col-md-6 col-sm-6 col-xs-12 mb-4"
                >
                  <div className="nft market-nft">
                    <CustomSlide className="itm" index={1}>
                      <div className="nft_coll">
                        <div className="nft_wrap">
                          <span
                            className=" pic-demo"
                            onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.accountId,
                                
                              })
                            }
                            style={{
                              background: `url(${httpUrl +
                                "/" +
                                item?.image?.replaceAll("\\", "/")
                                }) no-repeat`,
                            }}
                          >
                            <img
                              src={httpUrl + "/" + item?.image}
                              className="lazy img-more-collection"
                              alt=""
                            />
                          </span>
                        </div>
                        <div className="nft_coll_pp">
                          <span
                            onClick={() => {
                              history.push(
                                item.ownerAddress === WalletAddress
                                  ? `/myprofile`
                                  : `/profile/${item.ownerAddress}`
                              );
                            }}
                          >
                            <img
                              className="lazy"
                              src={
                                item?.ownerImage
                                  ? httpUrl + "/" + item?.ownerImage
                                  : defaultImg
                              }
                              alt=""
                            />
                          </span>
                        </div>
                        <div className="nft_coll_info">
                          <span
                            onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.accountId,
                                
                              })
                            }
                          >
                            {item?.name?.substring().length > 20 ? (
                              <h4> {`${item?.name.substring(0, 20)}...`}</h4>
                            ) : (
                              <>
                                <h4>{item?.name}</h4>
                              </>
                            )
                            }
                          </span>
                          <span
                           onClick={() =>
                            history.push({
                              pathname: `/usernftdetail/${item.id}`,
                              state: item.accountId,
                              
                            })
                          }
                          >
                            Price {" " + item?.sellPrice? item?.sellPrice: item?.buyPrice + " "}BNB
                          </span>
                          <div class="full-div">
                            <a
                              onClick={() =>
                                history.push({
                                  pathname: `/usernftdetail/${item.id}`,
                                  state: item.accountId,
                                  
                                })
                              }
                              class="view-all-btn"
                            >
                              Detail{" "}
                              <i
                                class="fa fa-angle-right"
                                aria-hidden="true"
                              ></i>
                            </a>
                            {GetFavouriteNft?.some(
                              (itm, index) => {

                                return (
                                  itm.nftTokenId === item.nftTokenId
                                );
                              }
                            ) ? (
                              <i
                                onClick={() => {

                                  removeToFavourite(
                                    item.id,
                                    item.ownerAddress,
                                  );
                                }}
                                className="fa fa-heart"
                                style={{ color: "red" }}
                              >
                                {" "}
                                {item?.nftFavouritesCount}
                              </i>
                            ) : (
                              <i
                                onClick={() => {
                                  addToFavourite(
                                    item?.id,
                                    item.ownerAddress,
                                  );
                                }}
                                className="fa fa-heart"
                              >
                                {" "}
                                {item?.nftFavouritesCount}
                              </i>
                            )}
                          </div>
                        </div>
                      </div>
                    </CustomSlide>
                  </div>
                </div>
              )}
            </>
          ))}

          {/* {userNftData?.length > numItems && (
            <div className="col-lg-12">
              <div className="spacer-single"></div>
              <span onClick={loadMore} className="btn-main lead m-auto">
                Load More
              </span>
            </div>
          )} */}
        </>
      )}
    </div>
  );
}

export default OnSaleUserNfts;
