import React, { useEffect, useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import erc20Abi from "../../../metamask/erc20";
import { PulseLoader } from "react-spinners";
import GetCurrencyThroughBlockChainIdAction, {
  GetCurrencyThroughBlockChainIdRequest,
} from "../../../Redux/Actions/CurrencyAction/GetCurrencyThroughBlockChainIdAction";

function LiquiditySecondModel({
  showModal,
  hideModal,
  setSecondSelectedBlockChain,
  secondSelectedBlockChain,
  setSecondSelectedCurrency,
  secondSelectedCurrency,
  firstSelectedCurrency,
  firstInputRef,
  secondInputRef,
  setCurrencyCalculationData,
}) {
  const [isLoading, setIsLoading] = useState(false);
  const [show, setShow] = useState(false);
  const dispatch = useDispatch();
  const [currencyThroughId, setCurrencyThroughId] = useState([]);
  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );

  const GetAllBlockChain = useSelector(
    (state) => state?.GetAllBlockChain?.GetAllBlockChainResponse?.data
  );

  const GetCurrencyThroughBlockChainId = useSelector(
    (state) =>
      state?.GetCurrencyThroughBlockChainId
        ?.GetCurrencyThroughBlockChainIdResponse?.data
  );

  useEffect(() => {
    setShow(showModal);
    if (
      secondSelectedCurrency &&
      Object.keys(secondSelectedCurrency).length == 0
    ) {
      setCurrencyThroughId(GetCurrencyThroughBlockChainId);
    }
    return () => {
      dispatch(GetCurrencyThroughBlockChainIdRequest());
    };
  }, [showModal]);

  const handleClose = () => {
    setShow(false);
    hideModal(false);
  };

  const HandleSecondBlockChain = async (secondBlockChainId) => {
    setIsLoading(true);
    const extractedSecondBlockchain = GetAllBlockChain?.find((item, index) => {
      return item.chainID == secondBlockChainId;
    });

    setSecondSelectedBlockChain(extractedSecondBlockchain);
    await dispatch(
      GetCurrencyThroughBlockChainIdAction(extractedSecondBlockchain?.chainID)
    )
      .then((res) => {
        if (res.data[0]?.chainId == parseInt(window.ethereum.chainId)) {
          var currencyBalanceOnSameChain = [];
          res.data?.forEach(async (item, index) => {
            if (item?.smartContractAddress) {
              currencyBalanceOnSameChain.push(
                await getTokenBalance(
                  item?.smartContractAddress,
                  WalletAddress,
                  item
                )
              );
              setTimeout(() => {
                setCurrencyThroughId(currencyBalanceOnSameChain);
                setIsLoading(false);
              }, 1000);
            } else {
              const balanceOfNativeCurrency = await window.web3.eth.getBalance(
                WalletAddress
              );

              const convertedToWei = await window.web3.utils.fromWei(
                balanceOfNativeCurrency,
                "ether"
              );

              currencyBalanceOnSameChain.push({
                ...item,
                balance: convertedToWei.substr(0, 6),
              });
              return;
            }
          });
        } else {
          setCurrencyThroughId(res?.data);
          setIsLoading(false);
        }
      })
      .catch(() => {});
  };

  const getTokenBalance = async (tokenAddress, accountAddr, item) => {
    if (tokenAddress && accountAddr) {
      const contract = new window.web3.eth.Contract(erc20Abi, tokenAddress);
      return await contract.methods
        .balanceOf(accountAddr)
        .call()
        .then(async (data) => {
          const balance = await window.web3.utils.fromWei(data, "ether");
          return { ...item, balance };
          console.log(data);
        });
    }
  };

  const handleSecondCurrency = (currenyRecord) => {
    setSecondSelectedCurrency(currenyRecord);
    setShow(false);
    hideModal(false);
    firstInputRef.current.value = "";
    secondInputRef.current.value = "";
    setCurrencyCalculationData({});
  };

  return (
    <>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title className="text-dark">Second Currency</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <select
            className="form-control "
            onChange={(e) => {
              const { value } = e.target;
              HandleSecondBlockChain(value);
            }}
          >
            <option value="" style={{ display: "none" }}>
              Select BlockChain
            </option>
            {GetAllBlockChain?.map((item, index) => {
              return (
                <option
                  key={item.id + 30}
                  value={item.chainID}
                  selected={secondSelectedBlockChain?.chainID == item.chainID}
                >
                  {item.name}
                </option>
              );
            })}
          </select>

          <ul style={{ listStyle: "none", marginTop: "15px" }}>
            {!isLoading ? (
              currencyThroughId?.map((item, index) => {
                return (
                  item.name != firstSelectedCurrency.name && (
                    <li
                      key={item.id + 10}
                      onClick={() => handleSecondCurrency(item)}
                      className="w-100 d-flex justify-content-between"
                    >
                      <div>
                        {item?.image ? (
                          <img src={item?.image} width={30} height={30} />
                        ) : (
                          ""
                        )}
                        {item?.name}
                      </div>
                      {item?.balance
                        ? parseFloat(item?.balance).toFixed(3)
                        : ""}
                    </li>
                  )
                );
              })
            ) : (
              <div className="text-center">
                <PulseLoader color="blue" size="11" />
              </div>
            )}
          </ul>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default LiquiditySecondModel;
