import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const AddLiquidityRequest = createAction("AddLiquidity_REQUEST");
export const AddLiquiditySuccess = createAction("AddLiquidity_SUCCESS");
export const AddLiquidityFail = createAction("AddLiquidity_FAIL");

const AddLiquidityAction = (body) => (dispatch) => {
  // alert(body);
  dispatch(AddLiquidityRequest());
  return new Promise((resolve) => {
    return Api.AddLiquidity.AddLiquidityApi(body)
      .then(({ data }) => {
        dispatch(AddLiquiditySuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(AddLiquidityFail(error));
        resolve();
      });
  });
};
export default AddLiquidityAction;
