import { handleActions } from "redux-actions";

const initialState = {
  AddWhiteLabelAddressResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const AddWhiteLabelAddress = handleActions(
  {
    AddWhiteLabelAddress_REQUEST: (state) => ({
      ...state,
      AddWhiteLabelAddressResponse: {
        ...state.defaultData,
      },
    }),
    AddWhiteLabelAddress_SUCCESS: (state, { payload }) => ({
      ...state,
      AddWhiteLabelAddressResponse: payload,
    }),
    AddWhiteLabelAddress_FAIL: (state, { payload }) => ({
      ...state,
      AddWhiteLabelAddressResponse: payload.data,
    }),
    AddWhiteLabelAddress_REQUEST_CLEAR: (state) => ({
      ...state,
      AddWhiteLabelAddressResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default AddWhiteLabelAddress;
