import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const DeleteWhiteLabelAddressRequest = createAction(
  "DeleteWhiteLabelAddress_REQUEST"
);
export const DeleteWhiteLabelAddressSuccess = createAction(
  "DeleteWhiteLabelAddress_SUCCESS"
);
export const DeleteWhiteLabelAddressFail = createAction(
  "DeleteWhiteLabelAddress_FAIL"
);

const DeleteWhiteLabelAddressAction = (body) => (dispatch) => {
  dispatch(DeleteWhiteLabelAddressRequest());
  return new Promise((resolve, reject) => {
    return Api.DeleteWhiteLabelAddress.DeleteWhiteLabelAddressApi(body)
      .then(({ data }) => {
        dispatch(DeleteWhiteLabelAddressSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(DeleteWhiteLabelAddressFail(error));
        reject(error);
      });
  });
};
export default DeleteWhiteLabelAddressAction;
