import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const GetCurrencyThroughBlockChainIdRequest = createAction(
  "GetCurrencyThroughBlockChainId_REQUEST"
);
export const GetCurrencyThroughBlockChainIdSuccess = createAction(
  "GetCurrencyThroughBlockChainId_SUCCESS"
);
export const GetCurrencyThroughBlockChainIdFail = createAction(
  "GetCurrencyThroughBlockChainId_FAIL"
);

const GetCurrencyThroughBlockChainIdAction = (body) => (dispatch) => {
  dispatch(GetCurrencyThroughBlockChainIdRequest());
  return new Promise((resolve, reject) => {
    return Api.GetCurrencyThroughBlockChainId.GetCurrencyThroughBlockChainIdApi(
      body
    )
      .then(({ data }) => {
        dispatch(GetCurrencyThroughBlockChainIdSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(GetCurrencyThroughBlockChainIdFail(error));
        reject(error);
      });
  });
};
export default GetCurrencyThroughBlockChainIdAction;
