import http from "../http";
const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;

export default class GetAllMasterWalletByCurrencyId {
  static GetAllMasterWalletByCurrencyIdApi(body) {
    return http.get(
      httpUrl + "/api/v1/MasterWallet/GetAllMasterWalletByCurrencyId?Id=" + body
    );
  }
}
