import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const PupolarViewsNftRequest = createAction("PupolarViewsNft_REQUEST");
export const PupolarViewsNftSuccess = createAction("PupolarViewsNft_SUCCESS");
export const PupolarViewsNftFail = createAction("PupolarViewsNft_FAIL");

const PupolarViewsNftAction = (body) => (dispatch) => {
  dispatch(PupolarViewsNftRequest());
  return new Promise((resolve, reject) => {
    return Api.PupolarViewsNft.PupolarViewsNftApi(body)
      .then(({ data }) => {
        dispatch(PupolarViewsNftSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(PupolarViewsNftFail(error));
        reject();
      });
  });
};
export default PupolarViewsNftAction;
