import { handleActions } from "redux-actions";

const initialState = {
  GetAllMasterWalletByCurrencyIdResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const GetAllMasterWalletByCurrencyId = handleActions(
  {
    GetAllMasterWalletByCurrencyId_REQUEST: (state) => ({
      ...state,
      GetAllMasterWalletByCurrencyIdResponse: {
        ...state.defaultData,
      },
    }),
    GetAllMasterWalletByCurrencyId_SUCCESS: (state, { payload }) => ({
      ...state,
      GetAllMasterWalletByCurrencyIdResponse: payload,
    }),
    GetAllMasterWalletByCurrencyId_FAIL: (state, { payload }) => ({
      ...state,
      GetAllMasterWalletByCurrencyIdResponse: payload.data,
    }),
    GetAllMasterWalletByCurrencyId_REQUEST_CLEAR: (state) => ({
      ...state,
      GetAllMasterWalletByCurrencyIdResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default GetAllMasterWalletByCurrencyId;
