import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";
import { RingLoader } from "react-spinners";
import GetWhiteLabelAddressByChainIDAction from "../../../Redux/Actions/WhiteLabelAddressAction/GetWhiteLabelAddressByChainIDAction";
import AddWhiteLableModel from "../../components/WhiteLableAddressPopupModel/AddWhiteLableModel";
import DeleteWhiteLabelAddressAction from "../../../Redux/Actions/WhiteLabelAddressAction/DeleteWhiteLabelAddressAction";
const WhiteLableAddress = () => {
  const dispatch = useDispatch();
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [openConfirmModel, setOpenConfirmModel] = useState(false);
  const [apiResponse, setApiResponse] = useState(true);
  const isConnected = useSelector(
    (state) => state.Login?.authResponse?.data?.token
  );

  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );
  const WalletAddresChainId = useSelector(
    (state) => state?.WalletConnction?.WalletResponse?.chainId
  );
  console.log(WalletAddresChainId);

  const GetWhiteLabelAddressByChainID = useSelector(
    (state) =>
      state?.GetWhiteLabelAddressByChainID
        ?.GetWhiteLabelAddressByChainIDResponse?.data
  );

  console.log("white lable address", GetWhiteLabelAddressByChainID);
  const columns = [
    {
      Header: "Date",
      accessor: "col1", // accessor is the "key" in the data
    },
    {
      Header: "Address",
      accessor: "col2",
    },
    {
      Header: "Label",
      accessor: "col3",
    },
    {
      Header: "Blockchain",
      accessor: "col5",
    },

    {
      Header: "",
      accessor: "col6",
    },
  ];

  useEffect(async () => {
    if (isConnected) {
      setIsLoading(true);
      // setWhitelistLabel(WalletAddress);
      setTimeout(async () => {
        await dispatch(GetWhiteLabelAddressByChainIDAction(chainId))
          .then((res) => {
            setIsLoading(false);
            setData(res?.data);
          })
          .catch(() => {
            setIsLoading(false);
          });
      }, 2000);
      //have to chain after controlling the multi chain
      const chainId = parseInt(WalletAddresChainId);
      console.log(chainId);
    }
  }, [isConnected]);

  const handleConfirm = (e) => {
    e.preventDefault();
    setOpenConfirmModel(true);
  };

  const deleteAddress = async (uuid) => {
    await dispatch(DeleteWhiteLabelAddressAction(uuid))
      .then((response) => {
        if (response.isSuccess) {
          // setApiResponse(response);
          // message.success("Address is deleted successfully");
          var haveToDelete = data.find((data) => data.uuid == uuid);
          console.log(data.indexOf(haveToDelete));
          data.splice(data.indexOf(haveToDelete), 1);
          var updatedData = [...data];
          console.log("prooooooo", data);
          setData(updatedData);
          toast.error(`${response?.data}`, {
            position: "top-right",

            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        // message.error(error.data.message);
        // setApiResponse(error);
      });
  };

  return (
    <>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <div className=" container liquidity-swap">
        <div>
          <h1>Wallet Addresses</h1>
        </div>
        <div className="w-100 text-right">
          <button
            className="reg-btn mb-3"
            onClick={(e) => {
              handleConfirm(e);
            }}
          >
            Add New Address
          </button>
        </div>

        <AddWhiteLableModel
          showModal={openConfirmModel}
          hideModal={setOpenConfirmModel}
          setData={setData}
        />
        {isLoading ? (
          <div className="text-center">
            <RingLoader color="white" size="60" />
          </div>
        ) : (
          <table className="table">
            <thead>
              <tr
                style={{
                  borderBottom: "solid 1px white",
                }}
              >
                {columns.map((data) => {
                  return <th className="text-light">{data.Header}</th>;
                })}
              </tr>
            </thead>
            <tbody>
              {data?.map((item, index) => (
                <tr
                  style={{
                    borderBottom: "solid 1px #AAAAAA",
                  }}
                  key={index}
                >
                  <td className="text-light">
                    {item?.createdAt.split("T")[0]}
                  </td>
                  <td className="text-light">{item?.address}</td>
                  <td className="text-light">
                    <button
                      style={{
                        //   backgroundColor: data.label === "Auto" ? "green" : "#0079fc",
                        borderRadius: "6px",
                        padding: "4px 6px",
                        border: "none",
                      }}
                    >
                      {item?.label}
                    </button>
                  </td>

                  <td className="text-light">{item?.blockChainName}</td>
                  <td
                    style={{ paddingRight: "10px" }}
                    onClick={() => {
                      deleteAddress(item?.uuid);
                    }}
                  >
                    <a
                      href="#"
                      className="btn btn-danger"
                      onClick={(e) => {
                        e.preventDefault();
                      }}
                    >
                      Delete
                    </a>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </>
  );
};

export default WhiteLableAddress;
