import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const GetUserFavouriteNftRequest = createAction("GetUserFavouriteNft_REQUEST");
export const GetUserFavouriteNftSuccess = createAction("GetUserFavouriteNft_SUCCESS");
export const GetUserFavouriteNftFail = createAction("GetUserFavouriteNft_FAIL");

const GetUserFavouriteNftAction = (body) => (dispatch) => {
  // dispatch(GetUserFavouriteNftRequest());
  return new Promise((resolve) => {
    return Api.GetUserFavouriteNft.GetUserFavouriteNftApi(body)
      .then(({ data }) => {
        dispatch(GetUserFavouriteNftSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(GetUserFavouriteNftFail(error));
        resolve();
      });
  });
};
export default GetUserFavouriteNftAction;
