import React, { useEffect, useState } from "react";
import { Button, Row, Modal, Col, H3 } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import {
  faArrowDown,
  faQuestionCircle,
  faSyncAlt,
} from "@fortawesome/free-solid-svg-icons";

export default function SwapConfirmModel(props) {
  const handleConfirm = () => {
    props.hideModal(false);
    props.action();
  };

  const handleClose = () => {
    props.hideModal(false);
  };

  return (
    <>
      <Modal
        show={props.showModal}
        onHide={handleClose}
        // backdrop='static'
        // keyboard={false}
      >
        <Modal.Header className={"border-0 p-4 "} closeButton>
          <Modal.Title className={"text-dark"}>Confirm Swap</Modal.Title>
        </Modal.Header>
        <Modal.Body className={"ps-4 pt-0 pb-0 pe-4"}>
          <Row className="flex-column">
            <Row className="justify-content-between">
              <Col xs={5}>
                <span>
                  <strong>From Currency: </strong>
                </span>{" "}
              </Col>
              <Col xs={5}>
                <span>
                  {props.fromSelectedCurrency?.image && (
                    <img
                      src={props.fromSelectedCurrency?.image}
                      width={30}
                      height={30}
                    />
                  )}{" "}
                  {props.data?.fromCurrencyName}
                </span>
              </Col>
            </Row>
            <Row className="justify-content-between">
              <Col xs={4}>
                <span>
                  <strong>From Amount: </strong>
                </span>
              </Col>
              <Col xs={5}>
                <span>
                  <span style={{ marginLeft: 35 }}></span>{" "}
                  {props.data?.fromAmount}
                </span>
              </Col>
            </Row>
            <Row className="justify-content-between">
              {" "}
              <Col xs={4}>
                <span>
                  <strong>To Currency: </strong>
                </span>
              </Col>
              <Col xs={5}>
                <span>
                  {props.toSelectedCurrency?.image && (
                    <img
                      src={props.toSelectedCurrency?.image}
                      width={30}
                      height={30}
                    />
                  )}{" "}
                  {props.data?.toCurrencyName}
                </span>
              </Col>
            </Row>
            <Row className="justify-content-between">
              <Col xs={5}>
                <span>
                  <strong>To Amount: </strong>
                </span>
              </Col>
              <Col xs={5}>
                <span>
                  <span style={{ marginLeft: 35 }}></span>{" "}
                  {props.data?.toAmount?.toFixed(14)}
                </span>
              </Col>
            </Row>
          </Row>
        </Modal.Body>
        <Modal.Footer className="flex-column border-0 p-4">
          <Button
            variant="primary w-100"
            // className={}
            size="lg"
            onClick={handleConfirm}
          >
            Confirm Swap
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
