import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const CalculationRequest = createAction("Calculation_REQUEST");
export const CalculationSuccess = createAction("Calculation_SUCCESS");
export const CalculationFail = createAction("Calculation_FAIL");

const CalculationAction = (body) => (dispatch) => {
  // alert(body);
  dispatch(CalculationRequest());
  return new Promise((resolve, reject) => {
    return Api.Calculation.CalculationApi(body)
      .then(({ data }) => {
        dispatch(CalculationSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(CalculationFail(error));
        reject(error);
      });
  });
};
export default CalculationAction;
