import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const GetWhiteLabelAddressByChainIDRequest = createAction(
  "GetWhiteLabelAddressByChainID_REQUEST"
);
export const GetWhiteLabelAddressByChainIDSuccess = createAction(
  "GetWhiteLabelAddressByChainID_SUCCESS"
);
export const GetWhiteLabelAddressByChainIDFail = createAction(
  "GetWhiteLabelAddressByChainID_FAIL"
);

const GetWhiteLabelAddressByChainIDAction = (body) => (dispatch) => {
  dispatch(GetWhiteLabelAddressByChainIDRequest());
  return new Promise((resolve, reject) => {
    return Api.GetWhiteLabelAddressByChainID.GetWhiteLabelAddressByChainIDApi(
      body
    )
      .then(({ data }) => {
        dispatch(GetWhiteLabelAddressByChainIDSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(GetWhiteLabelAddressByChainIDFail(error));
        reject(error);
      });
  });
};
export default GetWhiteLabelAddressByChainIDAction;
