import React, { useEffect, useState, useRef, Component } from "react";
import { useDispatch, useSelector } from "react-redux";
import Footer from "../components/footer";
import nftpic from "../../assets/images/nft-pic.png";
import Web3 from "web3";
import erc20Abi from "../../metamask/erc20";
import axios from "axios";
import LiquidityFirstModel from "../components/liquidityPopupModel/LiquidityFirstModel";
import LiquiditySecondModel from "../components/liquidityPopupModel/LiquiditySecondModel";
import CalculationAction, {
  CalculationRequest,
} from "../../Redux/Actions/SwapAction/CalculationAction";
import { Alert } from "react-bootstrap";
import defaultImg from "../../assets/images/default.png";
import { useHistory } from "react-router-dom";
import { sendToken, sendTransection, switchMetamask } from "../../metamask";
import { PulseLoader } from "react-spinners";
import AddLiquidityAction from "../../Redux/Actions/LiquidityAction/AddLiquidityAction";
import RefundTransactionAction from "../../Redux/Actions/LiquidityAction/RefundTransactionAction";
import { toast, ToastContainer } from "react-toastify";
import LiquidityConfirmModel from "../components/LiquidityConfirmModel/LiquidityConfirmationModel";
import PupolarViewsNftAction from "../../Redux/Actions/PupolarViewsNftAction/PupolarViewsNftAction";
import RemoveFavouriteNftAction from "../../Redux/Actions/NftActions/RemoveFavouriteNftAction";
import GetFavouriteNftAction from "../../Redux/Actions/NftActions/GetFavouriteNftAction";
class CustomSlide extends Component {
  render() {
    const { index, ...props } = this.props;
    return <div {...props}></div>;
  }
}

const Liquidity = () => {
  const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;
  const [openConfirmModel, setOpenConfirmModel] = useState(false);
  const history = useHistory();
  const handleConfirm = (e) => {
    e.preventDefault();
    setOpenConfirmModel(true);
  };
  const dispatch = useDispatch();
  const GetAllBlockChain = useSelector(
    (state) => state?.GetAllBlockChain?.GetAllBlockChainResponse?.data
  );

  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );

  const WalletAddresChainId = useSelector(
    (state) => state?.WalletConnction?.WalletResponse?.chainId
  );

  const isConnected = useSelector(
    (state) => state.Login?.authResponse?.data?.token
  );

  const GetAllCurrency = useSelector(
    (state) => state?.GetAllCurrency?.GetAllCurrencyResponse?.data
  );

  const balance = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.balance
  );

  const GetFavouriteNft = useSelector(
    (state) => state.GetFavouriteNft?.GetFavouriteNftResponse?.data
  );
  const Token = useSelector((state) => state.Login?.authResponse?.data?.token);
  const firstInputRef = useRef();
  const secondInputRef = useRef();
  const [updatedBalance, setUpdatedBalance] = useState();
  const [firstPopModal, setFirstPopModal] = useState(false);
  const [secondPopModal, setSecondPopModal] = useState(false);
  const [firstAvailableBalanceError, setFirstAvailableBalanceError] =
    useState(false);
  const [secondAvailableBalanceError, setSecondAvailableBalanceError] =
    useState(false);

  const transactionHashOneStorage = localStorage.getItem("transactionHashOne");

  const [transactionHashOne, setTransactionHashOne] = useState(
    transactionHashOneStorage ? JSON.parse(transactionHashOneStorage) : ""
  );
  const [transactionHashTwo, setTransactionHashTwo] = useState("");

  const [currencyCalculationData, setCurrencyCalculationData] = useState({});
  console.log("currencyCalculationData", currencyCalculationData);

  const [firstSelectedBlockChain, setFirstSelectedBlockChain] = useState({});
  const [secondSelectedBlockChain, setSecondSelectedBlockChain] = useState({});
  const [isLoading, setIsLoading] = useState(false);

  const [firstSelectedCurrency, setFirstSelectedCurrency] = useState({});
  const [secondSelectedCurrency, setSecondSelectedCurrency] = useState({});
  const [startMultiLiquidity, setStartMultiLiquidity] = useState(false);
  const [popularNft, setPopularNft] = useState();

  useEffect(() => {
    setTimeout(() => {
      setStartMultiLiquidity(true);
    }, 4000);
  }, []);

  useEffect(async () => {
    if (startMultiLiquidity) {
      const firstSelectedCurrencyStorage = localStorage.getItem(
        "firstSelectedCurrency"
      );

      const secondSelectedCurrencyStorage = localStorage.getItem(
        "secondSelectedCurrency"
      );

      const firstSelectedBlockChainStorage = localStorage.getItem(
        "firstSelectedBlockChain"
      );

      const secondSelectedBlockChainStorage = localStorage.getItem(
        "secondSelectedBlockChain"
      );

      const currencyCalculationDataStorage = localStorage.getItem(
        "currencyCalculationData"
      );

      if (
        firstSelectedCurrencyStorage &&
        secondSelectedCurrencyStorage &&
        firstSelectedBlockChainStorage &&
        secondSelectedBlockChainStorage &&
        currencyCalculationDataStorage
      ) {
        // ...item,
        // balance: convertedToWei.substr(0, 6),
        if (
          JSON.parse(firstSelectedCurrencyStorage).chainId ==
          parseInt(WalletAddresChainId)
        ) {
          var fromBalance;
          if (JSON.parse(firstSelectedCurrencyStorage).smartContractAddress) {
            fromBalance = await getTokenBalance(
              JSON.parse(firstSelectedCurrencyStorage)?.smartContractAddress,
              WalletAddress
            );
            setTimeout(() => {
              setFirstSelectedCurrency({
                ...JSON.parse(firstSelectedCurrencyStorage),
                balance: fromBalance,
              });
            }, 1500);
          } else {
            fromBalance = Web3.utils.fromWei(String(balance || 0));
            setTimeout(() => {
              setFirstSelectedCurrency({
                ...JSON.parse(firstSelectedCurrencyStorage),
                balance: fromBalance,
              });
            }, 2000);
          }
        } else {
          setFirstSelectedCurrency(JSON.parse(firstSelectedCurrencyStorage));
        }

        if (
          JSON.parse(secondSelectedCurrencyStorage).chainId ==
          parseInt(WalletAddresChainId)
        ) {
          var toBalance;
          if (JSON.parse(secondSelectedCurrencyStorage).smartContractAddress) {
            toBalance = await getTokenBalance(
              JSON.parse(secondSelectedCurrencyStorage)?.smartContractAddress,
              WalletAddress
            );
            setTimeout(() => {
              setSecondSelectedCurrency({
                ...JSON.parse(secondSelectedCurrencyStorage),
                balance: toBalance,
              });
            }, 2000);
          } else {
            toBalance = Web3.utils.fromWei(String(balance || 0));
            setTimeout(() => {
              setSecondSelectedCurrency({
                ...JSON.parse(secondSelectedCurrencyStorage),
                balance: toBalance,
              });
            }, 2000);
          }
        } else {
          setSecondSelectedCurrency(JSON.parse(secondSelectedCurrencyStorage));
        }

        setFirstSelectedBlockChain(JSON.parse(firstSelectedBlockChainStorage));
        setSecondSelectedBlockChain(
          JSON.parse(secondSelectedBlockChainStorage)
        );
        setCurrencyCalculationData(JSON.parse(currencyCalculationDataStorage));
      }
    }
  }, [startMultiLiquidity]);

  useEffect(() => {
    const convertedBalance = Web3.utils.fromWei(String(balance || 0));
    setUpdatedBalance(convertedBalance);
  }, [balance]);

  useEffect(() => {
    const firstSelectedCurrencyStorage = localStorage.getItem(
      "firstSelectedCurrency"
    );

    const secondSelectedCurrencyStorage = localStorage.getItem(
      "secondSelectedCurrency"
    );
    setTimeout(() => {
      if (
        firstSelectedCurrencyStorage &&
        secondSelectedCurrencyStorage &&
        secondSelectedCurrency &&
        Object.keys(secondSelectedCurrency).length != 0 &&
        Object.keys(firstSelectedCurrency).length != 0 &&
        firstSelectedCurrency &&
        currencyCalculationData
      ) {
        StartLiquidity();
      }
    }, 3000);
  }, [firstSelectedCurrency, secondSelectedCurrency, currencyCalculationData]);

  useEffect(() => {
    setSecondSelectedCurrency(secondSelectedCurrency);
  }, [secondSelectedCurrency]);
  useEffect(() => {
    setFirstSelectedCurrency(firstSelectedCurrency);
  }, [firstSelectedCurrency]);

  useEffect(() => {
    setTransactionHashOne(transactionHashOne);
  }, [transactionHashOne]);

  const getTokenBalance = async (tokenAddress, accountAddr) => {
    if (tokenAddress && accountAddr) {
      const contract = new window.web3.eth.Contract(erc20Abi, tokenAddress);
      return await contract.methods
        .balanceOf(accountAddr)
        .call()
        .then(async (data) => {
          const balance = await window.web3.utils.fromWei(data, "ether");
          return balance;
          console.log(data);
        });
    }
  };

  const handleCalculation = async (e) => {
    const { value, target } = e.target;
    if (value == "") {
      setFirstAvailableBalanceError(false);
    }
    const re = /^\s*(?=.*[1-9])\d*(?:\.\d{1,5})?\s*$/g;

    const payload = {
      fromCurrencyUuid: firstSelectedCurrency.uuid,
      toCurrencyUuid: secondSelectedCurrency.uuid,
      fromAmount: value,
    };
    if (re.test(value)) {
      await dispatch(CalculationAction(payload))
        .then((res) => {
          console.log(res?.data);
          setCurrencyCalculationData(res?.data);
          if (res?.data?.fromAmount >= firstSelectedCurrency?.balance) {
            setFirstAvailableBalanceError(true);
            secondInputRef.current.value = "";
          } else {
            setFirstAvailableBalanceError(false);
          }
          if (res?.data?.toAmount >= secondSelectedCurrency?.balance) {
            setSecondAvailableBalanceError(true);
            // secondInputRef.current.value = "";
          } else {
            setSecondAvailableBalanceError(false);
          }
        })
        .catch(() => {
          setCurrencyCalculationData({});
        });
    } else {
      secondInputRef.current.value = "";
      setCurrencyCalculationData({});
    }
  };

  const StartLiquidity = async (e) => {
    setIsLoading(true);
    if (e) {
      e.preventDefault();
    }

    const transactionHashOneStorage =
      localStorage.getItem("transactionHashOne");

    // const firstSelectedCurrencyStorage = localStorage.getItem(
    //   "firstSelectedCurrency"
    // );

    // var globalfirstSelectedCurrencyStorage = JSON.parse(
    //   firstSelectedCurrencyStorage
    // );

    if (transactionHashOneStorage && JSON.parse(transactionHashOneStorage)) {
      setTimeout(() => {
        StartSecondLiquidity();
      }, 2000);
      return;
    }

    if (
      Object.keys(firstSelectedCurrency)?.length != 0 &&
      firstSelectedCurrency.chainId != parseInt(WalletAddresChainId)
    ) {
      const firstSelectedCurrencyReadyForStorage = { ...firstSelectedCurrency };

      delete firstSelectedCurrencyReadyForStorage.balance;

      const secondSelectedCurrencyReadyForStorage = {
        ...secondSelectedCurrency,
      };

      delete secondSelectedCurrencyReadyForStorage.balance;

      localStorage.setItem(
        "firstSelectedCurrency",
        JSON.stringify(firstSelectedCurrencyReadyForStorage)
      );
      localStorage.setItem(
        "secondSelectedCurrency",
        JSON.stringify(secondSelectedCurrencyReadyForStorage)
      );

      localStorage.setItem(
        "firstSelectedBlockChain",
        JSON.stringify(firstSelectedBlockChain)
      );

      localStorage.setItem(
        "secondSelectedBlockChain",
        JSON.stringify(secondSelectedBlockChain)
      );

      localStorage.setItem(
        "currencyCalculationData",
        JSON.stringify(currencyCalculationData)
      );

      await switchMetamask(firstSelectedCurrency?.chainId)
        .then((res) => {})
        .catch((err) => {
          setIsLoading(false);

          resetStorageOnly();

          toast.error(`${err?.message}`, {
            position: "top-right",

            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: undefined,
          });
        });

      return;
    }
    const TransferFirstAmount = parseInt(
      Web3.utils.toWei(String(currencyCalculationData?.fromAmount.toFixed(18)))
    ).toString(16);

    if (firstSelectedCurrency?.currencyType == "Token") {
      const payload = {
        tokenAddress: firstSelectedCurrency?.smartContractAddress,

        from: WalletAddress,
        to: currencyCalculationData?.swapDepositAddress,
        value: currencyCalculationData?.fromAmount,
      };

      await sendToken(payload)
        .then(async (FirstTransactionHash) => {
          setTransactionHashOne(FirstTransactionHash);
          localStorage.setItem(
            "transactionHashOne",
            JSON.stringify(FirstTransactionHash)
          );
          localStorage.setItem("FirstTransactionComplete", true);
          setTimeout(() => {
            StartSecondLiquidity();
          }, 2000);
        })
        .catch((error) => {
          resetStorageOnly();
          setIsLoading(false);
          toast.error(`Transaction is rejected`, {
            position: "top-right",

            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: undefined,
          });
        });
    } else if (firstSelectedCurrency?.currencyType == "Blockchain") {
      console.log({ secondSelectedCurrency, firstSelectedCurrency });
      const accounts = await window.web3.eth.getAccounts();

      const payload = [
        {
          from: WalletAddress,
          to: currencyCalculationData?.swapDepositAddress,
          value: TransferFirstAmount,
        },
      ];
      await dispatch(sendTransection(payload))
        .then((FirstTransactionHash) => {
          setTransactionHashOne(FirstTransactionHash);
          localStorage.setItem(
            "transactionHashOne",
            JSON.stringify(FirstTransactionHash)
          );
          localStorage.setItem("FirstTransactionComplete", true);
          setTimeout(() => {
            StartSecondLiquidity();
          }, 2000);
        })
        .catch((error) => {
          resetStorageOnly();
          setIsLoading(false);
          toast.error(`Transaction is rejected`, {
            position: "top-right",

            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: undefined,
          });
        });
    }
  };

  const StartSecondLiquidity = async (e) => {
    setIsLoading(true);
    if (e) {
      e.preventDefault();
    }
    const transactionHashOneStorage =
      localStorage.getItem("transactionHashOne");

    if (secondSelectedCurrency.chainId != parseInt(WalletAddresChainId)) {
      const firstSelectedCurrencyReadyForStorage = { ...firstSelectedCurrency };

      delete firstSelectedCurrencyReadyForStorage.balance;

      const secondSelectedCurrencyReadyForStorage = {
        ...secondSelectedCurrency,
      };

      delete secondSelectedCurrencyReadyForStorage.balance;

      localStorage.setItem(
        "firstSelectedCurrency",
        JSON.stringify(firstSelectedCurrencyReadyForStorage)
      );
      localStorage.setItem(
        "secondSelectedCurrency",
        JSON.stringify(secondSelectedCurrencyReadyForStorage)
      );

      localStorage.setItem(
        "firstSelectedBlockChain",
        JSON.stringify(firstSelectedBlockChain)
      );

      localStorage.setItem(
        "secondSelectedBlockChain",
        JSON.stringify(secondSelectedBlockChain)
      );

      localStorage.setItem(
        "currencyCalculationData",
        JSON.stringify(currencyCalculationData)
      );

      await switchMetamask(secondSelectedCurrency?.chainId)
        .then((response) => {})
        .catch(async (error) => {
          setIsLoading(false);

          toast.error(`${error.message}`, {
            position: "top-right",

            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: undefined,
          });
          const payload = {
            transactionHash: JSON.parse(transactionHashOneStorage),
            walletaddress: WalletAddress,
            currencyName: firstSelectedCurrency.name,
            currencyUuid: currencyCalculationData.fromCurrencyUuid,
            amount: currencyCalculationData.fromAmount,
          };
          await dispatch(RefundTransactionAction(payload))
            .then((res) => {
              resetSomeState();
              toast.success(`${res.data}`, {
                position: "top-right",

                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
              });
            })
            .catch(() => {});
        });

      return;
    }
    const TransferSecondAmount = parseInt(
      Web3.utils.toWei(String((currencyCalculationData?.toAmount).toFixed(18)))
    ).toString(16);

    if (secondSelectedCurrency?.currencyType == "Token") {
      const payload = {
        tokenAddress: secondSelectedCurrency?.smartContractAddress,
        from: WalletAddress,
        to: currencyCalculationData?.swapDepositAddress,
        value: currencyCalculationData?.toAmount,
      };

      await sendToken(payload)
        .then(async (transactionHash) => {
          const payload = {
            currencyOneUuid: currencyCalculationData.fromCurrencyUuid,
            amountCurrency1: currencyCalculationData.fromAmount,
            transactionHashOne: JSON.parse(transactionHashOneStorage),
            currencyTwoUuid: currencyCalculationData.toCurrencyUuid,
            amountCurrency2: currencyCalculationData.toAmount,
            transactionHashTwo: transactionHash,
          };

          await dispatch(AddLiquidityAction(payload))
            .then(async (res) => {
              toast.success(`Liquidity added successfully`, {
                position: "top-right",

                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
              });

              resetSomeState();
              setIsLoading(false);
            })
            .catch((error) => {
              setIsLoading(false);
            });
        })
        .catch(async (error) => {
          const payload = {
            transactionHash: JSON.parse(transactionHashOneStorage),
            walletaddress: WalletAddress,
            currencyName: firstSelectedCurrency.name,
            currencyUuid: currencyCalculationData.fromCurrencyUuid,
            amount: currencyCalculationData.fromAmount,
          };

          await dispatch(RefundTransactionAction(payload))
            .then((response) => {
              setIsLoading(false);
              resetSomeState();
              toast.success(`${response?.data}`, {
                position: "top-right",

                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
              });
            })
            .catch(() => {
              setIsLoading(false);
              // resetSomeState();
            });
        });
    } else if (secondSelectedCurrency?.currencyType == "Blockchain") {
      console.log({ secondSelectedCurrency, firstSelectedCurrency });

      const payload = [
        {
          from: WalletAddress,
          to: currencyCalculationData?.swapDepositAddress,
          value: TransferSecondAmount,
        },
      ];

      await dispatch(sendTransection(payload))
        .then(async (transactionHash) => {
          const payload = {
            currencyOneUuid: currencyCalculationData.fromCurrencyUuid,
            amountCurrency1: currencyCalculationData.fromAmount,
            transactionHashOne: JSON.parse(transactionHashOneStorage),
            currencyTwoUuid: currencyCalculationData.toCurrencyUuid,
            amountCurrency2: currencyCalculationData.toAmount,
            transactionHashTwo: transactionHash,
          };

          await dispatch(AddLiquidityAction(payload))
            .then(async (res) => {
              toast.success(`Liquidity added successfully`, {
                position: "top-right",

                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
              });

              resetSomeState();
              setIsLoading(false);
            })
            .catch((error) => {
              setIsLoading(false);
            });
        })
        .catch(async (error) => {
          const payload = {
            transactionHash: JSON.parse(transactionHashOneStorage),
            walletaddress: WalletAddress,
            currencyName: firstSelectedCurrency.name,
            currencyUuid: currencyCalculationData.fromCurrencyUuid,
            amount: currencyCalculationData.fromAmount,
          };

          await dispatch(RefundTransactionAction(payload))
            .then((response) => {
              setIsLoading(false);
              resetSomeState();
              toast.success(`${response?.data}`, {
                position: "top-right",

                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
              });
            })
            .catch(() => {
              setIsLoading(false);
              // resetSomeState();
            });
        });
    }
  };

  const resetStorageOnly = () => {
    localStorage.removeItem("firstSelectedCurrency");
    localStorage.removeItem("secondSelectedCurrency");
    localStorage.removeItem("firstSelectedBlockChain");
    localStorage.removeItem("secondSelectedBlockChain");
    localStorage.removeItem("currencyCalculationData");
    localStorage.removeItem("transactionHashOne");
    localStorage.removeItem("FirstTransactionComplete");
  };

  const resetSomeState = () => {
    localStorage.removeItem("firstSelectedCurrency");
    localStorage.removeItem("secondSelectedCurrency");
    localStorage.removeItem("firstSelectedBlockChain");
    localStorage.removeItem("secondSelectedBlockChain");
    localStorage.removeItem("currencyCalculationData");
    localStorage.removeItem("transactionHashOne");
    localStorage.removeItem("FirstTransactionComplete");

    dispatch(CalculationRequest());
    setTimeout(() => {
      setSecondSelectedCurrency({});
      setFirstSelectedCurrency({});
      setSecondSelectedBlockChain({});
      setFirstSelectedBlockChain({});
      setCurrencyCalculationData({});

      firstInputRef.current.value = "";
      secondInputRef.current.value = "";
    }, 500);
  };

  useEffect(async () => {
    await dispatch(GetFavouriteNftAction())
      .then(() => {})
      .catch(() => {});
    await dispatch(PupolarViewsNftAction())
      .then((res) => {
        // console.log("popular Nfts", res.data);
        const PopNfts = res?.data;
        const PopularSortedNfts = PopNfts.sort((a, b) => {
          return b.viewCount - a.viewCount;
        });

        setPopularNft(PopularSortedNfts);
      })
      .catch((err) => {
        console.log("popular Nfts", err);
      });

    // http
    //   .get(httpUrl + "/api/v1/Nft/PupolarViewsNft?PageSize=2")
    //   .then(async (res) => {
    //     setPopularNft(res.data.data);
    //   })
    //   .catch((error) => {
    //     console.log(error?.message);
    //   });

    // setTimeout(() => {
    //   setStartMultiSwap(true);
    // }, 4000);
  }, []);

  const removeToFavourite = async (nftId, OwnerAddress) => {
    const payload = {
      nftId: nftId,
      nftAddress: OwnerAddress,
    };
    await dispatch(RemoveFavouriteNftAction(payload))
      .then(async (resp) => {
        if (resp?.isSuccess === true) {
          const updateFavouriteNumber = popularNft.map((item) => {
            if (nftId == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount - 1,
              };
            } else {
              return {
                ...item,
              };
            }
          });
          setPopularNft([...updateFavouriteNumber]);
          toast.error(`${resp?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          await dispatch(GetFavouriteNftAction())
            .then(() => {})
            .catch(() => {});
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        // swal({
        //   icon: "error",
        //   title: error?.data?.message,
        // });
      });
  };

  const addToFavourite = async (nftID, OwnerAddress) => {
    if (!isConnected) {
      toast.error(`Please connect to wallet first`, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return;
    }

    const payload = {
      nftId: nftID,
      nftAddress: OwnerAddress,
    };
    await axios
      .post(
        httpUrl + "/api/v1/Nft/AddFavouriteNft",
        {
          nftId: nftID,
          nftAddress: OwnerAddress,
        },
        {
          headers: {
            Authorization: `Bearer ${Token}`,
          },
        }
      )
      .then(async (resp) => {
        if (resp?.data?.isSuccess === true) {
          const updateFavouriteNumber = popularNft.map((item) => {
            if (nftID == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount + 1,
              };
            } else {
              return {
                ...item,
              };
            }
          });
          setPopularNft([...updateFavouriteNumber]);
          toast.success(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });

          await dispatch(GetFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`NFT Already Liked`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        // swal({
        //   icon: "error",
        //   title: error?.data?.message,
        // });
      });
  };

  return (
    <>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <section className="container-fluid liquidity-swap height-box">
        <div className="row">
          <div className="container">
            <div className="row">
              <div className="col-lg-3 col-sm-12 col-md-4 col-12">
                <h2>Most Popular</h2>
                {console.log("Popular NFT", popularNft)}
                {popularNft?.map((item, index) => {
                  return (
                    index < 2 && (
                      <>
                        <div
                          key={index}
                          className="d-item col-lg-12 col-md-12 col-sm-12 col-xs-12"
                        >
                          <div className="nft market-nft">
                            <CustomSlide className="itm" index={1}>
                              <div className="nft_coll">
                                <div className="nft_wrap">
                                  <span
                                    className=" pic-demo"
                                    onClick={() => {
                                      history.push(
                                        `/usernftdetail/${item?.id}/${item?.accountId}`
                                      );
                                    }}
                                    style={{
                                      background: `url(${
                                        httpUrl +
                                        "/" +
                                        item?.image?.replaceAll("\\", "/")
                                      }) no-repeat`,
                                    }}
                                  >
                                    <img
                                      src={httpUrl + "/" + item?.image}
                                      className="lazy popular-img"
                                      alt=""
                                    />
                                  </span>
                                </div>
                                <div className="nft_coll_pp">
                                  <span
                                    onClick={() => {
                                      history.push(
                                        item.ownerAddress === WalletAddress
                                          ? `/myprofile`
                                          : `/profile/${item.ownerAddress}`
                                      );
                                    }}
                                  >
                                    <img
                                      className="lazy"
                                      src={
                                        item?.ownerImage
                                          ? httpUrl + "/" + item?.ownerImage
                                          : defaultImg
                                      }
                                      alt=""
                                    />
                                  </span>
                                </div>
                                <div className="nft_coll_info">
                                  <span
                                    onClick={() => {
                                      history.push(
                                        `/usernftdetail/${item?.id}/${item?.accountId}`
                                      );
                                    }}
                                  >
                                    {item?.name?.substring().length > 20 ? (
                                      <h4>
                                        {" "}
                                        {`${item?.name.substring(0, 20)}...`}
                                      </h4>
                                    ) : (
                                      <>
                                        <h4>{item?.name}</h4>
                                      </>
                                    )}
                                  </span>
                                  <span
                                    onClick={() => {
                                      history.push(
                                        `/usernftdetail/${item?.id}/${item?.accountId}`
                                      );
                                    }}
                                  >
                                    Price {" " + item?.buyPrice + " "}BNB
                                  </span>
                                  <div class="full-div">
                                    <a
                                      onClick={() => {
                                        history.push(
                                          `/usernftdetail/${item?.id}/${item?.accountId}`
                                        );
                                      }}
                                      class="view-all-btn"
                                    >
                                      Detail{" "}
                                      <i
                                        class="fa fa-angle-right"
                                        aria-hidden="true"
                                      ></i>
                                    </a>
                                    {GetFavouriteNft?.some((itm, index) => {
                                      return itm.nftTokenId === item.nftTokenId;
                                    }) ? (
                                      <i
                                        onClick={() => {
                                          removeToFavourite(
                                            item.id,
                                            item.ownerAddress
                                          );
                                        }}
                                        className="fa fa-heart"
                                        style={{ color: "red" }}
                                      >
                                        {" "}
                                        {item?.nftFavouritesCount}
                                      </i>
                                    ) : (
                                      <i
                                        onClick={() => {
                                          addToFavourite(
                                            item?.id,
                                            item.ownerAddress
                                          );
                                        }}
                                        className="fa fa-heart"
                                      >
                                        {" "}
                                        {item?.nftFavouritesCount}
                                      </i>
                                    )}
                                  </div>
                                </div>
                              </div>
                            </CustomSlide>
                          </div>
                        </div>
                        <div className="full-div text-center">
                          <div className="border-div"></div>
                        </div>
                      </>
                    )
                  );
                })}
              </div>
              <div className="col-lg-9 col-sm-12 col-md-8 col-12">
                <div className="liquidity-swap-pnl">
                  <div className="text-pnl">
                    <h1>Liquidity</h1>
                    {firstAvailableBalanceError && (
                      <Alert variant={"danger"}>
                        First amount is greater than available balance
                      </Alert>
                    )}

                    {secondAvailableBalanceError && (
                      <Alert variant={"danger"}>
                        Second amount is greater than available balance
                      </Alert>
                    )}

                    <form>
                      <div className="row">
                        <div className="col-lg-12 col-sm-12 col-md-12 col-12">
                          <div className="full-div swap-input-pnl">
                            <label>Amount</label>
                            <span className="float-right">
                              {firstSelectedCurrency?.balance
                                ? `Balance: ${parseFloat(
                                    firstSelectedCurrency?.balance
                                  ).toFixed(3)}`
                                : firstSelectedCurrency?.chainId ==
                                  parseInt(window.ethereum.chainId)
                                ? `Balance:${parseFloat(updatedBalance).toFixed(
                                    3
                                  )}`
                                : ""}
                            </span>
                            <div className="row">
                              <div className="col-lg-6 col-sm-12 col-md-6 col-12">
                                <input
                                  type="number"
                                  name="liquidity-input"
                                  id="liquidity-input"
                                  ref={firstInputRef}
                                  defaultValue={
                                    currencyCalculationData?.fromAmount
                                  }
                                  className="form-control"
                                  placeholder="0.0"
                                  disabled={
                                    !(
                                      Object.keys(firstSelectedCurrency)
                                        .length != 0 &&
                                      Object.keys(secondSelectedCurrency)
                                        ?.length != 0
                                    )
                                  }
                                  onChange={(e) => {
                                    handleCalculation(e);
                                  }}
                                />
                              </div>
                              <div className="col-lg-4 offset-lg-2 col-sm-12 col-md-6 col-12">
                                <div
                                  className="select_currency_swap p-2"
                                  onClick={() => setFirstPopModal(true)}
                                >
                                  <span>
                                    {Object.keys(firstSelectedCurrency)
                                      ?.length === 0
                                      ? "Select Currency"
                                      : firstSelectedCurrency?.name}
                                  </span>
                                </div>
                                <LiquidityFirstModel
                                  showModal={firstPopModal}
                                  hideModal={setFirstPopModal}
                                  setFirstSelectedBlockChain={
                                    setFirstSelectedBlockChain
                                  }
                                  firstSelectedBlockChain={
                                    firstSelectedBlockChain
                                  }
                                  setFirstSelectedCurrency={
                                    setFirstSelectedCurrency
                                  }
                                  firstSelectedCurrency={firstSelectedCurrency}
                                  secondSelectedCurrency={
                                    secondSelectedCurrency
                                  }
                                  firstInputRef={firstInputRef}
                                  secondInputRef={secondInputRef}
                                  setCurrencyCalculationData={
                                    setCurrencyCalculationData
                                  }
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-lg-12 col-sm-12 col-md-12 col-12">
                          <div className="full-div swap-input-pnl">
                            <label>Amount</label>
                            <span className="float-right">
                              {secondSelectedCurrency?.balance
                                ? `Balance: ${parseFloat(
                                    secondSelectedCurrency?.balance
                                  ).toFixed(3)}`
                                : secondSelectedCurrency?.chainId ==
                                  parseInt(window.ethereum.chainId)
                                ? `Balance:${parseFloat(updatedBalance).toFixed(
                                    3
                                  )}`
                                : ""}
                            </span>
                            <div className="row">
                              <div className="col-lg-6 col-sm-12 col-md-6 col-12">
                                <input
                                  type="number"
                                  ref={secondInputRef}
                                  name="liquidity-input"
                                  id="liquidity-input"
                                  value={currencyCalculationData?.toAmount}
                                  className="form-control"
                                  placeholder="0.0"
                                  disabled
                                />
                              </div>
                              <div className="col-lg-4 offset-lg-2 col-sm-12 col-md-6 col-12">
                                <div
                                  className="select_currency_swap p-2"
                                  onClick={() => setSecondPopModal(true)}
                                >
                                  <span>
                                    {Object.keys(secondSelectedCurrency)
                                      ?.length === 0
                                      ? "Select Currency"
                                      : secondSelectedCurrency.name}
                                  </span>
                                </div>
                                <LiquiditySecondModel
                                  showModal={secondPopModal}
                                  hideModal={setSecondPopModal}
                                  setSecondSelectedBlockChain={
                                    setSecondSelectedBlockChain
                                  }
                                  secondSelectedBlockChain={
                                    secondSelectedBlockChain
                                  }
                                  setSecondSelectedCurrency={
                                    setSecondSelectedCurrency
                                  }
                                  secondSelectedCurrency={
                                    secondSelectedCurrency
                                  }
                                  firstSelectedCurrency={firstSelectedCurrency}
                                  firstInputRef={firstInputRef}
                                  secondInputRef={secondInputRef}
                                  setCurrencyCalculationData={
                                    setCurrencyCalculationData
                                  }
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-lg-12 col-sm-12 col-md-12 col-12">
                          {!(
                            currencyCalculationData &&
                            Object.keys(currencyCalculationData)?.length != 0 &&
                            !firstAvailableBalanceError &&
                            !secondAvailableBalanceError
                          ) ? (
                            <button className=" reg-btn disable-btn" disabled>
                              Add Liquidity
                            </button>
                          ) : isLoading ? (
                            <button className="reg-btn">
                              <PulseLoader color="white" size="11" />
                            </button>
                          ) : (
                            <button
                              className="reg-btn"
                              onClick={(e) => {
                                handleConfirm(e);
                              }}
                            >
                              Add Liquidity
                            </button>
                          )}
                          <LiquidityConfirmModel
                            showModal={openConfirmModel}
                            hideModal={setOpenConfirmModel}
                            data={currencyCalculationData}
                            action={StartLiquidity}
                            firstSelectedCurrency={firstSelectedCurrency}
                            secondSelectedCurrency={secondSelectedCurrency}
                          />
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};
export default Liquidity;
