import { handleActions } from "redux-actions";

const initialState = {
  SwapCreateResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const SwapCreate = handleActions(
  {
    SwapCreate_REQUEST: (state) => ({
      ...state,
      SwapCreateResponse: {
        ...state.defaultData,
      },
    }),
    SwapCreate_SUCCESS: (state, { payload }) => ({
      ...state,
      SwapCreateResponse: payload,
    }),
    SwapCreate_FAIL: (state, { payload }) => ({
      ...state,
      SwapCreateResponse: payload.data,
    }),
    SwapCreate_REQUEST_CLEAR: (state) => ({
      ...state,
      SwapCreateResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default SwapCreate;
