import React, { useEffect, useState, useRef } from "react";
import Footer from "../components/footer";
import { createGlobalStyle } from "styled-components";
import "../../assets/myProfile.scss";
import * as Yup from "yup";
import { Formik, Form, Field } from "formik";
import { CopyToClipboard } from "react-copy-to-clipboard";

import { useSelector, useDispatch } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import UpdateProfileAction from "../../Redux/Actions/Account/UpdateProfileAction";
import MyProfileAction from "../../Redux/Actions/Account/MyProfileAction";

import { FaCopy, FaUserCircle } from "react-icons/fa";
import { ToastContainer, toast } from "react-toastify";
import { PulseLoader } from "react-spinners";

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    background: #212428;
  }
`;

const CreateSchema = Yup.object().shape({
  username: Yup.string()
    .required("Please enter the username without any spaces in this field")
    .matches(
      /^[A-Za-z0-9\.\-\/]+$/,
      "Only alphabets and number are allowed without space this field "
    ),
  bio: Yup.string(),
  email: Yup.string().required("Email Required"),
  instagramLink: Yup.string().url().nullable(true),
  twitterLink: Yup.string().url().nullable(true),
  yourSiteLink: Yup.string().url().nullable(true),
});

function ProfileSettings() {
  const [files, SetFiles] = useState();
  const [FileError, SetFileError] = useState("");
  const [userImage, setUserImage] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const formRef = useRef();

  const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;

  const MyProfile = useSelector(
    (state) => state.MyProfile?.MyProfileResponse?.data
  );
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(MyProfileAction())
      .then((res) => {
        const data = res.data;

        formRef.current.setValues({
          yourSiteLink:
          (data?.yourSiteLink &&
            data?.yourSiteLink != "null") || data?.yourSiteLink != null ? data?.yourSiteLink : "",
          username:
            (data?.username && data?.username != "null" && data?.username) ||
            "",
          bio: (data?.bio && data?.bio != "null" && data?.bio) || "",
          email: (data?.email && data?.email != "null" && data?.email) || "",

          instagramLink:
            (data?.instagramLink &&
              data?.instagramLink != "null") || data?.instagramLink != null ? data?.instagramLink : "",

          twitterLink:
          (data?.twitterLink &&
            data?.twitterLink != "null") || data?.twitterLink != null ? data?.twitterLink : "",
            
          WalletAdres: WalletAddress,
        });

        console.log(formRef.current.values);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const fileschange = (e) => {


    const file = e.target.files[0];
    if (
      file?.type === "image/jpeg" ||
      file?.type === "image/png" ||
      file?.type === "image/jpg" ||
      file?.type === "image/gif"
    ) {

      SetFileError(null);
      SetFiles(file);
    } else {
      SetFileError("Invalid File Format");
      SetFiles(null);
    }
  };

  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );

  const [ProfileData, SetProfileData] = useState({
    username: MyProfile?.username,
    bio: MyProfile?.bio,
    email: MyProfile?.email,
    instagramLink: MyProfile?.instagramLink,
    yourSiteLink: MyProfile?.yourSiteLink,
    twitterLink: MyProfile?.twitterLink,
    WalletAdres: WalletAddress,
  });

  const inputhandler = (e) => {
    const { name, value } = e.target;

    SetProfileData((pre) => {
      return {
        ...pre,
        [name]: value,
      };
    });
  };

  useEffect(() => {
    setUserImage(MyProfile?.profileImage);
    SetProfileData(MyProfile);
  }, [MyProfile]);

  const onsubmitHandler = async (e) => {
    setIsLoading(true);
    // e.preventDefault();
    // if (ProfileData.username == null) {
    //   setIsLoading(false);

    //   toast.success(`username required`, {
    //     position: "top-right",
    //     autoClose: 5000,
    //     hideProgressBar: false,
    //     closeOnClick: true,
    //     pauseOnHover: true,
    //     draggable: true,
    //     progress: undefined,
    //   });

    if (files == null && FileError == "Invalid File Format") {
      toast.error(
        `You selected the wrong file type, you can only upload PNG, JPG, JPEG, GIF`,
        {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        }
      );
      setIsLoading(false);
      return;
    }

    // }
    let tweetLink;
    let instaLink;
    let siteLink;

    if(ProfileData.twitterLink==="null"||ProfileData.twitterLink===""||ProfileData.twitterLink===null){
      tweetLink = '';
    } else {
      tweetLink = ProfileData.twitterLink;
    }

    if(ProfileData.instagramLink==="null"||ProfileData.instagramLink===""||ProfileData.instagramLink===null){
      instaLink = '';
    } else {
      instaLink = ProfileData.instagramLink;
    }

    if(ProfileData.yourSiteLink==="null"||ProfileData.yourSiteLink===""||ProfileData.yourSiteLink===null){
      siteLink = '';
    } else {
      siteLink = ProfileData.yourSiteLink;
    }

    var bodyFormData = new FormData();
    bodyFormData.append("ProfileImage", files);
    bodyFormData.append("Username", ProfileData.username);
    bodyFormData.append("Email", ProfileData.email);
    bodyFormData.append("TwitterLink", tweetLink);
    bodyFormData.append("InstagramLink", instaLink);
    bodyFormData.append("YourSiteLink", siteLink);
    bodyFormData.append("Bio", ProfileData.bio);
    console.log("body data", ...bodyFormData)
    await dispatch(UpdateProfileAction(bodyFormData))
      .then((res) => {
        console.log("abc");
        setIsLoading(false);
        toast.success(`${res.message}`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,

        });

        dispatch(MyProfileAction())
          .then((res) => {
            const data = res.data;

            formRef.current.setValues({
              yourSiteLink:
                (data?.yourSiteLink &&
                  data?.yourSiteLink != "null" &&
                  data?.yourSiteLink) ||
                "",
              username:
                (data?.username &&
                  data?.username != "null" &&
                  data?.username) ||
                "",
              bio: (data?.bio && data?.bio != "null" && data?.bio) || "",
              email:
                (data?.email && data?.email != "null" && data?.email) || "",
              instagramLink:
                (data?.instagramLink &&
                  data?.instagramLink != "null" &&
                  data?.instagramLink) ||
                "",
              twitterLink:
                (data?.twitterLink &&
                  data?.twitterLink != "null" &&
                  data?.twitterLink) ||
                "",
              WalletAdres: WalletAddress,
            });

          })
          .catch((error) => {
            console.log(error);
          });

        setUserImage(res?.data?.profileImage);
        setTimeout(() => {
          history.push("/myProfile")
        }, 5000);
      })
      .catch((error) => {
        console.log(error);

        toast.error(`${error?.message}`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  };
  return (
    <div>
      <GlobalStyles />
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <div className=" container-fluid imsettt">
        <div className="spacer-double"></div>
        <div className="row justify-content-center">
          <div className="col-md-12 text-center">
            <h1 className="text-white">Profile Setting</h1>
            <div className="full-div text-center">
              <div className="border-div"></div>
            </div>
          </div>
          <div className="col-md-12 col-lg-8">
            <div className="spacer-10"></div>
            <div className="row">
              <div className="col-md-12">
                <Formik
                  validationSchema={CreateSchema}
                  innerRef={formRef}
                  onSubmit={() => onsubmitHandler()}
                  validator={() => ({})}
                  initialValues={{
                    username: "",
                    bio: "",
                    email: "",
                    instagramLink: "",
                    twitterLink: "",
                    yourSiteLink: "",
                  }}
                >
                  {({
                    values,
                    errors,
                    touched,
                    handleSubmit,
                    handleChange,
                    setFieldValue,
                  }) => (
                    <Form
                      name="contactForm"
                      id="contact_form"
                      className="form-border"
                      onSubmit={handleSubmit}
                    >
                      {console.log(errors)}
                      <div className="row">
                        <div className="col-md-12">
                          <div className="row">
                            <div className="col-md-12 text-center">
                              <div htmlFor="imagee" className="changeimg">
                                {files ? (
                                  <img
                                    id="changePic"
                                    src={URL.createObjectURL(files)}
                                    alt="profilepic.png"
                                    style={{ width: 200, height: 200 }}
                                  />
                                ) : (
                                  MyProfile?.profileImage ? (
                                    <img
                                      id="changePic"

                                      src={`${httpUrl}/${userImage}`}
                                      alt="profilepic.png"
                                      style={{ width: 200, height: 200 }}
                                    />
                                  ) : (
                                    <FaUserCircle size="2x" />
                                  ))}
                              </div>
                            </div>
                            <div className="spacer-30"></div>
                            <div className="col-lg-12 text-center">



                              <div id="submitttt">
                                <Link to="/myprofile" className="btn-main">
                                  Preview Profile
                                </Link>
                              </div>
                              <div className="spacer-30"></div>
                            </div>
                            <div className="col-md-6">
                              <div className="field-set">
                                <label style={{color:"white"}}>Username:</label>
                                <input
                                  onChange={(e) => {
                                    inputhandler(e);
                                    handleChange(e);
                                  }}
                                  type="text"
                                  name="username"
                                  id="username"
                                  value={values.username}
                                  placeholder="Enter Your Username Here..."
                                  className="form-control"
                                  disabled={MyProfile?.username ?? false}
                                />
                              </div>
                              {errors.username && touched.username && (
                                <div className="text-red">
                                  {errors.username}
                                </div>
                              )}
                            </div>
                            <div className="col-md-6">
                              <div className="field-set">
                                <label style={{color:"white"}}>Email Address:</label>
                                <input
                                  placeholder="Enter Your Email Here..."
                                  onChange={(e) => {
                                    inputhandler(e);
                                    handleChange(e);
                                  }}
                                  type="email"
                                  value={values.email}
                                  name="email"
                                  id="email"
                                  className="form-control"
                                />
                              </div>
                              {errors.email && touched.email && (
                                <div className="text-red">{errors.email}</div>
                              )}
                            </div>
                            <div className="col-md-12">
                              <div className="field-set">
                                <label style={{color:"white"}}>Wallet Address:</label>
                                {WalletAddress ? (
                                  <span
                                    style={{
                                      wordBreak: "break-all",
                                    }}
                                    id="name"
                                    className="form-control br-r wallet-add"
                                  >
                                    {WalletAddress}{" "}
                                    <CopyToClipboard
                                      text={WalletAddress}
                                      onCopy={() => {
                                        toast.success(
                                          "Address copied successfully",
                                          {
                                            position: "top-right",
                                            autoClose: 5000,
                                            hideProgressBar: false,
                                            closeOnClick: true,
                                            pauseOnHover: false,
                                            draggable: true,
                                            progress: undefined,
                                          }
                                        );
                                      }}
                                    >
                                      <FaCopy
                                        style={{
                                          float: "right",
                                          fontSize: "25px",
                                          marginTop: "0px",
                                          cursor: "pointer",
                                        }}
                                        title="Copy to use paperclip"
                                      />
                                    </CopyToClipboard>

                                  </span>
                                ) : (
                                  <span
                                    style={{
                                      backgroundColor: "none",
                                      color: "white",
                                    }}
                                    id="name"
                                    className="form-control"
                                  >
                                    Login To View Wallet address
                                  </span>
                                )}
                              </div>
                              <div className="spacer-10"></div>
                            </div>
                            <div className="col-md-12">
                              <div className="field-set">
                                <div className="row">
                                  <div className="col-lg-12">
                                    <label style={{color:"white"}}>Social Links :</label>
                                  </div>

                                  <div className="col-lg-6">
                                    <input
                                      onChange={(e) => {
                                        inputhandler(e);
                                        handleChange(e);
                                      }}
                                      type="text"
                                      name="instagramLink"
                                      value={values.instagramLink}
                                      id="instagram"
                                      className="form-control"
                                      placeholder="e.g. https://instagram.com/"
                                    />
                                    {errors.instagramLink &&
                                      touched.instagramLink && (
                                        <div className="text-red">
                                          {errors.instagramLink}
                                        </div>
                                      )}
                                  </div>
                                  <div className="col-lg-6">
                                    <input
                                      onChange={(e) => {
                                        inputhandler(e);
                                        handleChange(e);
                                      }}
                                      type="text"
                                      name="twitterLink"
                                      id="twitter"
                                      value={values.twitterLink}
                                      className="form-control"
                                      placeholder="e.g. https://Twitter.com/"
                                    />
                                    {errors.twitterLink && touched.twitterLink && (
                                      <div className="text-red">
                                        {errors.twitterLink}
                                      </div>
                                    )}
                                  </div>
                                  <div className="col-lg-6">
                                    <input
                                      onChange={(e) => {
                                        inputhandler(e);
                                        handleChange(e);
                                      }}
                                      type="text"
                                      name="yourSiteLink"
                                      value={values.yourSiteLink}
                                      id="yourSiteLink"
                                      className="form-control"
                                      placeholder="e.g. https://YourSiteLink.com/"
                                    />
                                    {errors.yourSiteLink &&
                                      touched.yourSiteLink && (
                                        <div className="text-red">
                                          {errors.yourSiteLink}
                                        </div>
                                      )}
                                  </div>
                                </div>
                              </div>
                            </div>
                            <div className="col-md-12">
                              <div className="field-set">

                                <label style={{color:"white"}}>Update Profile Pic:</label>
                                <input
                                  onChange={fileschange}
                                  type="file"
                                  className="form-control profileInput"
                                />
                              </div>
                            </div>
                            <div className="col-md-12">
                              <div className="field-set">
                                <label style={{color:"white"}}>Bio:</label>
                                <textarea
                                  data-autoresize
                                  name="bio"
                                  id="bio"
                                  onChange={(e) => {
                                    inputhandler(e);
                                    handleChange(e);
                                  }}
                                  className="form-control"
                                  value={values.bio}
                                  placeholder="e.g. My Name Is Jhon"
                                ></textarea>
                              </div>
                              {errors.bio && touched.bio && (
                                <div className="text-red">{errors.bio}</div>
                              )}
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="row">
                      <div className="col-lg-12 btn-cntnr">
                        
                          {isLoading ? (
                            <button
                              style={{
                                backgroundColor: "#308AFB",
                                borderRadius: 10,
                                width: 130,
                              }}
                            >
                              <PulseLoader color="white" size="11" />
                            </button>
                          ) : (
                            <input
                              type="submit"
                              id="submit"
                              className="btn-main"
                              value="Save"
                            />
                          )}
                          <input
                            style={{
                              width: 150,
                              borderRadius: 10,
                            }}
                            value={"Cancel"}
                            className="btn-main whiter"
                            onClick={() => history.goBack()}
                          />
                        </div>
                      </div>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="spacer-double"></div>
      <Footer />
    </div>
  );
}

export default ProfileSettings;
