import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toast, ToastContainer } from "react-toastify";
import { RingLoader } from "react-spinners";
import LiquidityProvidersHistoryAction from "../../Redux/Actions/LiquidityAction/LiquidityProvidersHistoryAction";
import GetAllCurrencyAction from "../../Redux/Actions/CurrencyAction/GetAllCurrencyAction";
import LiquidityWithdrawlAction from "../../Redux/Actions/LiquidityAction/LiquidityWithdrawlAction";
const LiquidityStats = () => {
  const dispatch = useDispatch();
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [liquiditStatData, setLiquiditStatData] = useState([]);
  const isConnected = useSelector(
    (state) => state.Login?.authResponse?.data?.token
  );

  const LiquidityProvidersHistory = useSelector(
    (state) =>
      state?.LiquidityProvidersHistory?.LiquidityProvidersHistoryResponse?.data
  );

  const GetAllCurrency = useSelector(
    (state) => state?.GetAllCurrency?.GetAllCurrencyResponse?.data
  );

  // console.log(GetAllCurrency);

  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );
  const WalletAddresChainId = useSelector(
    (state) => state?.WalletConnction?.WalletResponse?.chainId
  );
  console.log(WalletAddresChainId);

  const GetWhiteLabelAddressByChainID = useSelector(
    (state) =>
      state?.GetWhiteLabelAddressByChainID
        ?.GetWhiteLabelAddressByChainIDResponse?.data
  );

  console.log("white lable address", GetWhiteLabelAddressByChainID);
  const columns = [
    {
      Header: "Currency One",
      accessor: "col1", // accessor is the "key" in the data
    },
    {
      Header: "Balance",
      accessor: "col2",
    },
    {
      Header: "Currency Two",
      accessor: "col3",
    },
    {
      Header: "Balance",
      accessor: "col5",
    },

    {
      Header: "",
      accessor: "col6",
    },
  ];

  useEffect(async () => {
    if (isConnected) {
      setIsLoading(true);
      // setWhitelistLabel(WalletAddress);
      setTimeout(async () => {
        await dispatch(LiquidityProvidersHistoryAction())
          .then((res) => {
            // console.log("Liquidty Provider History", res?.data);
            const resData = res.data;
            const updatedResData = resData.map((item) => {
              return { ...item, isDisabled: false };
            });
            console.log("Updated Data", updatedResData);
            setLiquiditStatData(updatedResData);
            // setData(res?.data);
            setIsLoading(false);
            // setData(res?.data);
          })
          .catch(() => {
            setIsLoading(false);
          });

        // await dispatch(GetAllCurrencyAction())
        //   .then(() => {})
        //   .catch(() => {});
      }, 1000);
      //have to chain after controlling the multi chain
      // const chainId = parseInt(WalletAddresChainId);
      // console.log(chainId);
    }
  }, [isConnected]);

  const StartClaimProcess = async (LiquidityHistoryRecord) => {
    // console.log(LiquidityHistoryRecord);
    // console.log(GetAllCurrency);
    // const extractedCurrencyId = GetAllCurrency.find(
    //   (item, index) => item.id == LiquidityHistoryRecord.currencyOneId
    // );

    const payload = {
      currencyPairId: LiquidityHistoryRecord?.pairId,
      walletAddress: WalletAddress,
    };

    const updatedResData = liquiditStatData.map((item) => {
      if (LiquidityHistoryRecord?.pairId == item.pairId) {
        return {
          ...item,
          isDisabled: true,
        };
      } else {
        return { ...item };
      }
    });
    setLiquiditStatData([...updatedResData]);

    await dispatch(LiquidityWithdrawlAction(payload))
      .then((res) => {
        toast.success(`${res.message}`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
        });

        console.log("Disabled Udated Data", updatedResData);
      })
      .catch((err) => {
        toast.error(`${err.message}`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
        });

        const updatedResData = liquiditStatData.map((item) => {
          if (LiquidityHistoryRecord?.pairId == item.pairId) {
            return {
              ...item,
              isDisabled: false,
            };
          } else {
            return { ...item };
          }
        });
        setLiquiditStatData([...updatedResData]);
      });
  };

  return (
    <>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <div className=" container liquidity-swap">
        <div>
          <h1>Liquidity Stats</h1>
        </div>
        {isLoading ? (
          <div className="text-center">
            <RingLoader color="white" size="60" />
          </div>
        ) : (
          <table className="table">
            <thead>
              <tr
                style={{
                  borderBottom: "solid 1px white",
                }}
              >
                {columns.map((data) => {
                  return <th className="text-light">{data.Header}</th>;
                })}
              </tr>
            </thead>
            <tbody>
              {liquiditStatData?.map((item, index) => (
                <tr
                  style={{
                    borderBottom: "solid 1px #AAAAAA",
                  }}
                  //   key={index}
                >
                  <td className="text-light">{item?.currencyOne}</td>
                  <td className="text-light">{item?.currencyOneBalance}</td>
                  <td className="text-light">{item?.currencyTwo}</td>

                  <td className="text-light">{item?.currencyTwoBalance}</td>
                  <td style={{ paddingRight: "10px" }}>
                    {GetAllCurrency ? (
                      <button
                        disabled={item?.isDisabled}
                        className={`reg-btn mt-0 ${
                          item?.isDisabled ? "disable-btn" : ""
                        }`}
                        onClick={(e) => {
                          e.preventDefault();
                          StartClaimProcess(item);
                        }}
                      >
                        Claim Reward
                      </button>
                    ) : (
                      ""
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}

        {/* <div className="w-100 text-right">
          <button
            className="reg-btn mb-3"
            // onClick={(e) => {
            //   handleConfirm(e);
            // }}
          >
            Claim Reward
          </button>
        </div> */}
      </div>
    </>
  );
};

export default LiquidityStats;
