import { handleActions } from "redux-actions";

const initialState = {
  PupolarViewsNftResponse: {},
};

const defaultData = {
  data: {},
  error: null,
  isSuccess: true,
  message: "",
};
const PupolarViewsNft = handleActions(
  {
    PupolarViewsNft_REQUEST: (state) => ({
      ...state,
      PupolarViewsNftResponse: {
        ...state.defaultData,
      },
    }),
    PupolarViewsNft_SUCCESS: (state, { payload }) => ({
      ...state,
      PupolarViewsNftResponse: payload,
    }),
    PupolarViewsNft_FAIL: (state, { payload }) => ({
      ...state,
      PupolarViewsNftResponse: payload.data,
    }),
    // GetMyAllNfts_REQUEST_CLEAR: (state) => ({
    //   ...state,
    //   PupolarViewsNftResponse: {
    //     ...state.defaultData,
    //   },
    // }),
  },
  initialState
);

export default PupolarViewsNft;
