import { handleActions } from "redux-actions";

const initialState = {
  CalculationLiquidityResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const CalculationLiquidity = handleActions(
  {
    CalculationLiquidity_REQUEST: (state) => ({
      ...state,
      CalculationLiquidityResponse: {
        ...state.defaultData,
      },
    }),
    CalculationLiquidity_SUCCESS: (state, { payload }) => ({
      ...state,
      CalculationLiquidityResponse: payload,
    }),
    CalculationLiquidity_FAIL: (state, { payload }) => ({
      ...state,
      CalculationLiquidityResponse: payload.data,
    }),
    CalculationLiquidity_REQUEST_CLEAR: (state) => ({
      ...state,
      CalculationLiquidityResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default CalculationLiquidity;
