import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Button, Row, Modal, Col, H3 } from "react-bootstrap";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";
import GetWhiteLabelAddressByChainIDAction from "../../../Redux/Actions/WhiteLabelAddressAction/GetWhiteLabelAddressByChainIDAction";
import AddWhiteLabelAddressAction from "../../../Redux/Actions/WhiteLabelAddressAction/AddWhiteLabelAddressAction";
export default function AddWhiteLableModel(props) {
  const dispatch = useDispatch();
  const [isEmpty, setIsEmpty] = useState(false);
  const [isNoteEmpty, setIsNoteEmpty] = useState(false);
  const [selectedBlockChain, setSelectedBlockChain] = useState();
  const WalletAddresChainId = useSelector(
    (state) => state?.WalletConnction?.WalletResponse?.chainId
  );
  const GetAllBlockChain = useSelector(
    (state) => state?.GetAllBlockChain?.GetAllBlockChainResponse?.data
  );

  useEffect(() => {
    if (GetAllBlockChain?.length > 0) {
      setSelectedBlockChain(GetAllBlockChain[0].chainID);
    }
  }, []);

  const [labelData, setLabelData] = useState({
    Name: "Ethereum",
    Address: "",
    BlockchainId: 0,
    IsWallet: true,
    chainId: 3,
    Note: "",
  });

  const registerWhiteLabel = async (e) => {
    e.preventDefault();
    if (labelData.Address.length == 0) {
      setIsEmpty(true);
      return;
    } else setIsEmpty(false);
    if (labelData.Note.length == 0) {
      setIsNoteEmpty(true);
      return;
    } else setIsNoteEmpty(false);

    // if (labelData.Name === 'Ethereum') {

    console.log("selectedBlockChainselectedBlockChain", selectedBlockChain);
    setLabelData((prev) => {
      return { ...prev, BlockchainId: selectedBlockChain };
    });
    // } else if (labelData.Name === 'Binance Smart Chain') {
    //   setLabelData((prev) => {
    //     return {...prev, BlockchainId: 4}
    //   })
    // }

    console.log(labelData);
    e.preventDefault();
    if (labelData.Address.length == 0) {
      setIsEmpty(true);
      return;
    } else setIsEmpty(false);
    if (labelData.Note.length == 0) {
      setIsNoteEmpty(true);
      return;
    } else setIsNoteEmpty(false);

    // if (labelData.Name === 'Ethereum') {

    console.log("selectedBlockChainselectedBlockChain", selectedBlockChain);
    setLabelData((prev) => {
      return { ...prev, BlockchainId: selectedBlockChain };
    });
    // } else if (labelData.Name === 'Binance Smart Chain') {
    //   setLabelData((prev) => {
    //     return {...prev, BlockchainId: 4}
    //   })
    // }

    console.log(labelData);
    // setInProgress(true)

    var bodyFormData = new FormData();
    bodyFormData.append("Name", labelData.Name);
    bodyFormData.append("Address", labelData.Address);
    bodyFormData.append("BlockchainId", selectedBlockChain);
    bodyFormData.append("IsWallet", labelData.IsWallet);
    bodyFormData.append("chainId", labelData.chainId);
    bodyFormData.append("Note", labelData.Note);

    // axios
    //   .post(
    //     httpUrl + "/api/v1/WhiteLabelAddress/AddWhiteLabelAddress",
    //     bodyFormData
    //   )
    await dispatch(AddWhiteLabelAddressAction(bodyFormData))
      .then((response) => {
        console.log("response from addwhitelabeladdress", response);
        handleClose();
        toast.success(`${response?.message}`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
        });
        setTimeout(async () => {
          await dispatch(GetWhiteLabelAddressByChainIDAction(chainId))
            .then((res) => {
              props.setData(res?.data);
            })
            .catch(() => {});
        }, 1000);
        //have to chain after controlling the multi chain
        const chainId = parseInt(WalletAddresChainId);
        console.log(chainId);
      })
      .catch((error) => {
        // message.error(error.data.message)
        // setInProgress(false)
        // setApiResponse(error)
        // //setAddedResponse(error)
        // setModalVisible(false)
        toast.error(`White Lable address is not addedd.`, {
          position: "top-right",

          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
        });
      });
  };

  const handleFormData = (event) => {
    const { name, value } = event.target;

    if (name === "Address") {
      if (value.length > 0) {
        setIsEmpty(false);
      } else setIsEmpty(true);
    }

    if (name === "Note") {
      if (value.length > 0) {
        setIsNoteEmpty(false);
      } else setIsNoteEmpty(true);
    }

    setLabelData((prev) => {
      return { ...prev, [name]: value };
    });

    // if (name === 'Name') {
    //   setSelectedBlockChain(value)
    // }

    console.log("labelData", labelData);
  };

  const handleSelectForm = (event) => {
    const { name, value } = event.target;

    console.log(value);
    const ExtractedBlockchain = GetAllBlockChain.find(
      (data) => data.chainID == value
    );

    setSelectedBlockChain(ExtractedBlockchain.chainID);
    setLabelData((prev) => {
      return {
        ...prev,
        Name: ExtractedBlockchain.name,
        chainId: ExtractedBlockchain.chainID,
      };
    });
  };

  const handleClose = () => {
    props.hideModal(false);
  };

  return (
    <>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <Modal
        show={props.showModal}
        onHide={handleClose}
        // backdrop='static'
        // keyboard={false}
      >
        <div
          style={{
            //backgroundColor: '#343D56',
            display: "flex",
            justifyContent: "space-between",
            marginBottom: "20px",
            boxSizing: "border-box",
            borderBottom: "2px solid #343D56",
            padding: "20px 20px 10px 20px",
          }}
        >
          <h4 style={{ color: "black" }}>Add New Address</h4>
          <p
            className="reg-btn add_close"
            // style={{ fontSize: "18px", borderRadius: "50%" }}
            onClick={handleClose}
          >
            x
          </p>
        </div>

        <form style={{ backgroundColor: "#fff" }} onSubmit={registerWhiteLabel}>
          <div className="container">
            <div className="d-flex flex-column ">
              <div className="d-flex justify-content-between mt-3 ">
                <label className="text-dark">Enter Blockchain Address</label>
                <input
                  id="address"
                  name="Address"
                  label="Enter Blockchain Address"
                  placeholder="Blockchain Address"
                  type="text"
                  onChange={handleFormData}
                  className="form-control w-50"
                />
              </div>
              {isEmpty ? (
                <p style={{ color: "red", textAlign: "left" }}>
                  This field is required
                </p>
              ) : null}
              <div className="d-flex justify-content-between mt-3">
                <label className="text-dark">Select Blockchain</label>
                <select
                  defaultValue={selectedBlockChain}
                  name="Name"
                  onChange={handleSelectForm}
                  className="form-control w-50"
                >
                  {GetAllBlockChain &&
                    GetAllBlockChain?.map((item) => {
                      return (
                        <option key={item.chainID} value={item.chainID}>
                          {item.name}({item.shortName})
                        </option>
                      );
                    })}
                </select>
              </div>
              <div className="d-flex justify-content-between mt-3">
                {" "}
                <label className="text-dark">Note</label>
                <input
                  id="note"
                  name="Note"
                  placeholder="Note"
                  type="text"
                  onChange={handleFormData}
                  className="form-control w-50"
                />
              </div>

              {isNoteEmpty ? (
                <p style={{ color: "red", textAlign: "left" }}>
                  This field is required
                </p>
              ) : null}
              <div className="w-100 text-center my-3">
                {" "}
                <button
                  type="submit"
                  className="reg-btn"
                  style={{ backgroundColor: "#0079fc" }}
                >
                  Add
                </button>
              </div>
            </div>
          </div>
        </form>
      </Modal>
    </>
  );
}
