import { handleActions } from "redux-actions";

const initialState = {
  LiquidityWithdrawlResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const LiquidityWithdrawl = handleActions(
  {
    LiquidityWithdrawl_REQUEST: (state) => ({
      ...state,
      LiquidityWithdrawlResponse: {
        ...state.defaultData,
      },
    }),
    LiquidityWithdrawl_SUCCESS: (state, { payload }) => ({
      ...state,
      LiquidityWithdrawlResponse: payload,
    }),
    LiquidityWithdrawl_FAIL: (state, { payload }) => ({
      ...state,
      LiquidityWithdrawlResponse: payload.data,
    }),
    LiquidityWithdrawl_REQUEST_CLEAR: (state) => ({
      ...state,
      LiquidityWithdrawlResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default LiquidityWithdrawl;
