import { handleActions } from "redux-actions";

const initialState = {
  GetWhiteLabelAddressByChainIDResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const GetWhiteLabelAddressByChainID = handleActions(
  {
    GetWhiteLabelAddressByChainID_REQUEST: (state) => ({
      ...state,
      GetWhiteLabelAddressByChainIDResponse: {
        ...state.defaultData,
      },
    }),
    GetWhiteLabelAddressByChainID_SUCCESS: (state, { payload }) => ({
      ...state,
      GetWhiteLabelAddressByChainIDResponse: payload,
    }),
    GetWhiteLabelAddressByChainID_FAIL: (state, { payload }) => ({
      ...state,
      GetWhiteLabelAddressByChainIDResponse: payload.data,
    }),
    GetWhiteLabelAddressByChainID_REQUEST_CLEAR: (state) => ({
      ...state,
      GetWhiteLabelAddressByChainIDResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default GetWhiteLabelAddressByChainID;
