import { handleActions } from "redux-actions";

const initialState = {
  AddWhiteLabelAddressShortResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const AddWhiteLabelAddressShort = handleActions(
  {
    AddWhiteLabelAddressShort_REQUEST: (state) => ({
      ...state,
      AddWhiteLabelAddressShortResponse: {
        ...state.defaultData,
      },
    }),
    AddWhiteLabelAddressShort_SUCCESS: (state, { payload }) => ({
      ...state,
      AddWhiteLabelAddressShortResponse: payload,
    }),
    AddWhiteLabelAddressShort_FAIL: (state, { payload }) => ({
      ...state,
      AddWhiteLabelAddressShortResponse: payload.data,
    }),
    AddWhiteLabelAddressShort_REQUEST_CLEAR: (state) => ({
      ...state,
      AddWhiteLabelAddressShortResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default AddWhiteLabelAddressShort;
