import { handleActions } from "redux-actions";

const initialState = {
  GetCurrencyThroughBlockChainIdResponse: {},
};

const defaultData = {
  data: {
    token: null,
    userInfo: {},
  },
  error: null,
  isSuccess: true,
  message: "",
};
const GetCurrencyThroughBlockChainId = handleActions(
  {
    GetCurrencyThroughBlockChainId_REQUEST: (state) => ({
      ...state,
      GetCurrencyThroughBlockChainIdResponse: {
        ...state.defaultData,
      },
    }),
    GetCurrencyThroughBlockChainId_SUCCESS: (state, { payload }) => ({
      ...state,
      GetCurrencyThroughBlockChainIdResponse: payload,
    }),
    GetCurrencyThroughBlockChainId_FAIL: (state, { payload }) => ({
      ...state,
      GetCurrencyThroughBlockChainIdResponse: payload.data,
    }),
    GetCurrencyThroughBlockChainId_REQUEST_CLEAR: (state) => ({
      ...state,
      GetCurrencyThroughBlockChainIdResponse: {
        ...state.defaultData,
      },
    }),
  },
  initialState
);

export default GetCurrencyThroughBlockChainId;
