import React, { useState, useEffect } from "react";
import { ethers } from "ethers";
import metamaskimg from "../../assets/images/metamask.svg";
import connectcircle from "../../assets/images/wallet.png";
import Footer from "../components/footer";
import { createGlobalStyle } from "styled-components";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { PulseLoader } from "react-spinners";
import connectMetaMaskaction from "../../Redux/Actions/WalletActions/WalletAction";
import AuthConnectAction from "../../Redux/Actions/AuthActions/AuthConnectAction";
import ValidateSignatureAction from "../../Redux/Actions/AuthActions/ValidateSignatureAction";
import { ToastContainer, toast } from "react-toastify";
import lottie from "lottie-web";
import connectionAnimation from "../../assets/animation/connection/data.json";
import http from "../../Redux/Api/http";
import { useLocation } from "react-router-dom";
const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;

function ConnectWallet(props) {
  const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    
  }
  .box-login p{
    color: #a2a2a2 !important;
  }
  .box-login{
    border-radius: 3px;
    padding: 40px 50px;
  }
`;

  const history = useHistory();
  const dispatch = useDispatch();
  const AuthConnectState = useSelector((state) => state.AuthConnect);
  const AuthConnect = AuthConnectState?.AuthConnectResponse?.data;
  const isConnected = useSelector(
    (state) => state.Login?.authResponse?.data?.token
  );
  const [isLoading, setIsLoading] = useState(false);
  const [trigger, setTrigger] = useState(false);
  const [chain, setChain] = useState(false);
  const [SigError, SetSigError] = useState();
  const dispatchConnect = () => dispatch(connectMetaMaskaction());
  var isUserLogedIn = false;
  const User = useSelector((state) => state.Login);
  const Tokenn = User.authResponse?.data?.token;
  const location = useLocation();

  const GetAllBlockChain = useSelector(
    (state) => state?.GetAllBlockChain?.GetAllBlockChainResponse?.data
  );

  if (
    User.authResponse &&
    User.authResponse.data &&
    User.authResponse?.data?.token &&
    chain
  ) {
    isUserLogedIn = true;
  } else {
    isUserLogedIn = false;
  }

  useEffect(() => {
    console.log(props.location.state);
    console.log(props.location.state);
    console.log(props.location.state);
    console.log(props.location.state);
  }, []);

  const connnectwallet = async () => {
    setTrigger(true);
    setIsLoading(true);
    if (window.ethereum) {
      await window.ethereum.enable().then(async (res) => {
        setTimeout(async () => {
          await dispatchConnect().then(async (res) => {
            await dispatch(AuthConnectAction({ address: res }))
              .then(async (response) => {
                // console.log(response);
                // setTimeout(async () => {
                if (response?.data?.message && !Tokenn) {
                  await signMessage(response?.data?.message);
                }
                // }, 1000);
              })
              .catch((error) => {
                setIsLoading(false);
                console.log(error);
              });
          });
        }, 1000);
      });
    } else {
      setIsLoading(false);
      toast.error(`Please Install Metamask Extension`, {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const chainCheck = async () => {
    const baseUrl = process.env.REACT_APP_DEVELOPMENT_URL;
    let chainPromise = await http
      .get(baseUrl + "/api/v1/BlockChain/GetBlockChainForNft")
      .then((res) => {
        const data = res.data.data;
        let check = data[0]?.chainID === parseInt(window.ethereum.chainId);
        return check;
      });

    return chainPromise;
  };

  const connectionFunc = async (res) => {
    await dispatch(AuthConnectAction({ address: res }))
      .then(async (response) => {
        // console.log(response);
        // setTimeout(async () => {
        if (response?.data?.message && !Tokenn) {
          await signMessage(response?.data?.message);
          setTrigger(false);
        }
        // }, 1000);
      })
      .catch((error) => {
        if (error.code == 4001 || error.status == 500) {
          console.log("noooo");
          connectionFunc(res);
        }
        // setIsLoading(false)
        console.log("error", error);
      });
  };

  useEffect(() => {
    lottie.loadAnimation({
      container: document.querySelector("#connectionCircle"),
      animationData: connectionAnimation,
      renderer: "svg", // "canvas", "html"
      loop: true, // boolean
      autoplay: true, // boolean
    });
  }, []);

  //  useEffect(async ()=>{
  //   const chains = await chainCheck();
  //   setChain(chains);
  //  }, [location.pathname])

  // useEffect(async () => {
  //   const chains = await chainCheck();
  //   console.log("useEffect Empty array", chains);
  //   setChain(chains);
  // }, []);

  const signMessage = async (message) => {
    await window.ethereum.send("eth_requestAccounts");
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const signature = await signer.signMessage(message);
    const address = await signer.getAddress();

    await dispatch(
      ValidateSignatureAction({
        address: address,
        signature: signature,
      })
    )
      .then((res) => {
        const chainCheck = GetAllBlockChain?.some((item, index) => {
          return item.chainID == parseInt(window.ethereum.chainId);
        });

        if (chainCheck) {
          toast.success(
            `${res.message} you are going to redirect to profile page`,
            {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            }
          );

          setTimeout(() => {
            history.push(props.location.state ? `${props.location.state.location}`:"/myprofile", props?.location?.state?.id ? props?.location?.state?.id: '');
          }, 3000);
        }

        setIsLoading(false);

        console.log(res);
      })
      .catch((err) => {
        console.log(err);
        setIsLoading(false);

        toast.error(`${err.message}`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  };

  return (
    <div>
      <GlobalStyles />
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <section className="jumbotron breadcumb no-bg">
        <div className="mainbreadcumb height-box">
          <div className="container">
            {GetAllBlockChain &&
            isConnected &&
            !GetAllBlockChain?.some((item, index) => {
              console.log("*************************", item);
              console.log(
                "*************************",
                parseInt(window.ethereum.chainId)
              );
              return item.chainID == parseInt(window.ethereum.chainId);
            }) ? (
              <div className="row align-items-center px-0">
                {console.log("inside return connect wallet", chain)}
                {/* {console.log("&*&*&*&*&&*&*&*&*&*&*&*&*&*&*&*&*&*&*&*&*&*")} */}
                <div className="col-lg-6 offset-lg-3 col-md-12 offset-md-0 m-auto px-0">
                  <div className="box-login text-center">
                    <h3 style={{ color: "white" }}>
                      Wrong blockchain selected make sure that you are in
                      Binance Smart Chain Network
                    </h3>
                  </div>
                </div>
              </div>
            ) : (
              <div className="row align-items-center px-0">
                <div className="col-lg-6 offset-lg-3 col-md-12 offset-md-0 m-auto px-0">
                  <div className="box-login">
                    <h1>Hello Mate!</h1>
                    <div className="connect-circle full-div text-center">
                      <div className="wallet-img">
                        <img src={connectcircle} alt="Connect Wallet" />
                      </div>
                    </div>
                    {/* <div
                      id="connectionCircle"
                      className="w-100 text-center"
                    >

                    </div> */}

                    <div className="mt-5">
                      {isLoading ? (
                        <div className="w-100">
                          <button
                            className="btn w-100"
                            style={{
                              backgroundColor: "#308AFB",
                              borderRadius: "30px",
                              fontWeight: "bold",
                            }}
                          >
                            <PulseLoader color="white" size="11" />
                          </button>
                        </div>
                      ) : isConnected ? (
                        <div className="field-set">
                          <input
                            type="submit"
                            id="send_message"
                            value="Connected"
                            className="btn btn-main btn-fullwidth color-2"
                            disabled
                          />
                        </div>
                      ) : (
                        <div className="field-set">
                          {GetAllBlockChain ? (
                            <input
                              type="submit"
                              id="send_message"
                              value="Connect Wallet "
                              className="btn btn-main btn-fullwidth color-2 trans"
                              onClick={connnectwallet}
                            />
                          ) : (
                            <></>
                          )}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
}

export default ConnectWallet;
