import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const AddWhiteLabelAddressShortRequest = createAction(
  "AddWhiteLabelAddressShort_REQUEST"
);
export const AddWhiteLabelAddressShortSuccess = createAction(
  "AddWhiteLabelAddressShort_SUCCESS"
);
export const AddWhiteLabelAddressShortFail = createAction(
  "AddWhiteLabelAddressShort_FAIL"
);

const AddWhiteLabelAddressShortAction = (body) => (dispatch) => {
  dispatch(AddWhiteLabelAddressShortRequest());
  return new Promise((resolve, reject) => {
    return Api.AddWhiteLabelAddressShort.AddWhiteLabelAddressShortApi(body)
      .then(({ data }) => {
        dispatch(AddWhiteLabelAddressShortSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(AddWhiteLabelAddressShortFail(error));
        reject(error);
      });
  });
};
export default AddWhiteLabelAddressShortAction;
