import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const SwapCreateRequest = createAction("SwapCreate_REQUEST");
export const SwapCreateSuccess = createAction("SwapCreate_SUCCESS");
export const SwapCreateFail = createAction("SwapCreate_FAIL");

const SwapCreateAction = (body) => (dispatch) => {
  // alert(body);
  dispatch(SwapCreateRequest());
  return new Promise((resolve, reject) => {
    return Api.SwapCreate.SwapCreateApi(body)
      .then(({ data }) => {
        dispatch(SwapCreateSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(SwapCreateFail(error));
        reject(error?.data);
      });
  });
};
export default SwapCreateAction;
