import React, { useEffect, useState, useRef, Component } from "react";

import { Link, useHistory } from "react-router-dom";
import { PropagateLoader, RingLoader } from "react-spinners";

import { useDispatch, useSelector } from "react-redux";
import { toast, ToastContainer } from "react-toastify";
import GetUserFavouriteNftAction from "../../../Redux/Actions/NftActions/GetUserFavouriteNftAction";
import axios from "axios";
import http from "../../../Redux/Api/http";
import RemoveFavouriteNftAction from "../../../Redux/Actions/NftActions/RemoveFavouriteNftAction";
import swal from "sweetalert";
import defaultImg from "../../../assets/images/default.png";


const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;

class CustomSlide extends Component {
  render() {
    const { index, ...props } = this.props;
    return <div {...props}></div>;
  }
}


function UserFavNft() {
  const history = useHistory();
  
  const dispatch = useDispatch();
  const searchRef = useRef();

  const [isloading, setIsloading] = useState(true);
  const [allData, setAllData] = useState([]);
  const [filterData, setFilterData] = useState([]);
  const [filterTrigger, setFilterTrigger] = useState(false);
  const [filter, setfilter] = useState([]);
  const [GetFavouriteNft, setGetFavouriteNft] = useState();
  const [height, Setheight] = useState(270);
  const [favourites, SetFavourites] = useState(GetFavouriteNft?.slice(0, 8));


  const isConnected = useSelector(
    (state) => state.Login?.authResponse?.data?.token
  );

  const Token = useSelector((state) => state.Login?.authResponse?.data?.token);

  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );

  // const GetFavouriteNft = useSelector(
  //   (state) => state.GetFavouriteNft?.GetFavouriteNftResponse?.data
  // );



  useEffect( async() => {

    await http
      .get(
        httpUrl +
        `/api/v1/Nft/GetMyFavouriteNft`
      )
      .then((res) => {
        setGetFavouriteNft(res.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
    SetFavourites(GetFavouriteNft?.slice(0, 8));
    // setAllData(GetFavouriteNft);
  }, [allData]);

  useEffect( async() => {
    let params = window.location.pathname;

    await http
      .get(
        httpUrl +
        `/api/v1/Nft/GetUserFavouriteNft?Address=${params.split("/")[2]}`
      )
      .then((res) => {
        setAllData(res.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  useEffect(async () => {
    await dispatch(GetUserFavouriteNftAction())
      .then((res) => {
        console.log("favourites response", res)
        setIsloading(false);
      })
      .catch((error) => {
        setIsloading(false);
        toast.success(`${error?.message}`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  }, []);

  const loadMore = () => {
    let nftState = favourites;
    let start = nftState?.length;
    let end = nftState?.length + 8;

    if (filterData?.length) {
      SetFavourites([...nftState, ...filterData?.slice(start, end)]);
    } else {
      SetFavourites([...nftState, ...GetFavouriteNft?.slice(start, end)]);
    }
  };

  const onImgLoad = ({ target: img }) => {
    let currentHeight = height;
    if (currentHeight < img.offsetHeight) {
      Setheight({
        height: img?.offsetHeight,
      });
    }
  };

  const handleSearchChange = (e) => {
    const { value } = e.target;

    setfilter(
      allData?.filter((item) =>
        item.name.toLowerCase().includes(value.toLowerCase())
      )
    );
  };

  const resetFilter = () => {
    SetFavourites(allData?.slice(0, 8));
    setfilter([]);
    setFilterTrigger(false);

    searchRef.current.value = "";
  };

  const handlerSearchSubmit = (e) => {
    e.preventDefault();
    setFilterTrigger(true);

    SetFavourites(filter?.slice(0, 8));
    setFilterData(filter);
  };


  const addToFavourite = async (nftID, OwnerAddress) => {
    if (!isConnected) {
      toast.error(`Please connect to wallet first`, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return;
    }

    // console.log("user token ", Token)
    await axios
      .post(
        httpUrl + "/api/v1/Nft/AddFavouriteNft",
        {
          nftId: nftID,
          nftAddress: OwnerAddress,
        },
        {
          headers: {
            Authorization: `Bearer ${Token}`,
          },
        }
      )
      .then(async (resp) => {
        if (resp?.data?.isSuccess === true) {
          const updateFavouriteNumber = allData.map((item) => {
            if (nftID == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount + 1,

              };
            } else {
              return {
                ...item,
              };
            }
          });
          setAllData([...updateFavouriteNumber]);
          toast.success(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });

          await dispatch(GetUserFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`NFT Already Liked`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        // swal({
        //   icon: "error",
        //   title: error?.data?.message,
        // });
      });
  };

  const removeToFavourite = async (nftId, OwnerAddress) => {
    const payload = {
      nftId: nftId,
      nftAddress: OwnerAddress,
    };

    await dispatch(RemoveFavouriteNftAction(payload))
      .then(async (resp) => {
        if (resp?.isSuccess === true) {
          const updateFavouriteNumber = allData.map((item) => {
            if (nftId == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount - 1,

              };
            } else {
              return {
                ...item,
              };
            }
          });
          setAllData([...updateFavouriteNumber]);
          toast.error(`${resp?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          await dispatch(GetUserFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        swal({
          icon: "error",
          title: error?.data?.message,
        });
      });
  };

  return (
    <>
      <div className="row">
        <ToastContainer
          position="top-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover={false}
        />
        {isloading ? (
          <>
            <div className="col-sm-12 d-flex justify-content-center">
              <RingLoader color="white" size="60" />
            </div>
          </>
        ) : (
          <>
            {console.log("favorites", GetFavouriteNft)}
            {allData === null ? (
              <div className="col-sm-12 text-center">No Favourite NFT Found</div>
            ) : (
              ""
            )}

            {allData?.map((item, index) => (
              <>

                <div
                  key={index}
                  className="d-item col-lg-3 col-md-6 col-sm-6 col-xs-12 mb-4"
                >
                  <div className="nft market-nft">
                    <CustomSlide className="itm" index={1}>
                      <div className="nft_coll">
                        <div className="nft_wrap">
                          <span
                            className=" pic-demo"
                            onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.userFvrtAccountId,
                                
                              })
                            }
                            style={{
                              background: `url(${httpUrl +
                                "/" +
                                item?.image?.replaceAll("\\", "/")
                                }) no-repeat`,
                            }}
                          >
                            <img
                              src={httpUrl + "/" + item?.image}
                              className="lazy img-more-collection"
                              alt=""
                            />
                          </span>
                        </div>

                        <div className="nft_coll_pp">
                          <span
                            onClick={() => {
                              console.log("profile clicked")
                              history.push(
                                item.ownerAddress === WalletAddress
                                  ? `/myprofile`
                                  : `/profile/${item.userFvrtAddress}`
                              );
                            }}
                          >
                            <img
                              className="lazy"
                              src={
                                item?.ownerImage
                                  ? httpUrl + "/" + item?.userProfileImageInFavrt
                                  : defaultImg
                              }
                              alt=""
                            />
                          </span>
                        </div>

                        <div className="nft_coll_info" style={{padding:"30px"}} >
                          <span
                            onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.userFvrtAccountId,
                                
                              })
                            }
                          >
                            {item?.name?.substring().length > 20 ? (
                              <h4> {`${item?.name.substring(0, 20)}...`}</h4>
                            ) : (
                              <>
                                <h4>{item?.name}</h4>
                              </>
                            )
                            }
                          </span>
                          <span
                            onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.userFvrtAccountId,
                                
                              })
                            }
                          >
                            Price {" " + item?.sellPrice? item?.sellPrice: item?.buyPrice + " "}BNB
                          </span>
                          <div class="full-div">
                            <a
                               onClick={() =>
                                history.push({
                                  pathname: `/usernftdetail/${item.id}`,
                                  state: item.userFvrtAccountId,
                                  
                                })
                              }
                              class="view-all-btn"
                            >
                              Detail{" "}
                              <i
                                class="fa fa-angle-right"
                                aria-hidden="true"
                              ></i>
                            </a>
                            {GetFavouriteNft?.some(
                              (itm, index) => {
                                return (
                                  itm.nftTokenId == item.nftTokenId
                                );
                              }
                            ) ? (
                              <i
                                onClick={() => {
                                  removeToFavourite(
                                    item.id,
                                    item.ownerAddress,
                                  );
                                }}
                                className="fa fa-heart"
                                style={{ color: "red" }}
                              >
                                {" "}
                                {item?.nftFavouritesCount}
                              </i>
                            ) : (
                              <i
                                onClick={() => {
                                  addToFavourite(
                                    item?.id,
                                    item.ownerAddress,
                                  );
                                }}
                                className="fa fa-heart"
                              >
                                {" "}
                                {item?.nftFavouritesCount}
                              </i>
                            )}
                          </div>
                        </div>
                      </div>
                    </CustomSlide>
                  </div>
                </div>



              </>
            ))}

            {filterData?.length && filterTrigger ? (
              <>
                {favourites?.length < filterData?.length && (
                  <div className="col-lg-12" style={{ textAlign: "center" }}>
                    <div className="spacer-single"></div>
                    <span onClick={loadMore} className="btn-main lead m-auto">
                      Load More
                    </span>
                  </div>
                )}
              </>
            ) : (
              <>
                {favourites?.length < allData?.length &&
                  !filterTrigger && (
                    <div className="col-lg-12" style={{ textAlign: "center" }}>
                      <div className="spacer-single"></div>
                      <span onClick={loadMore} className="btn-main lead m-auto">
                        Load More
                      </span>
                    </div>
                  )}
              </>
            )}
          </>
        )}
      </div>
    </>
  );
}

export default UserFavNft;
