import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const LiquidityWithdrawlRequest = createAction(
  "LiquidityWithdrawl_REQUEST"
);
export const LiquidityWithdrawlSuccess = createAction(
  "LiquidityWithdrawl_SUCCESS"
);
export const LiquidityWithdrawlFail = createAction("LiquidityWithdrawl_FAIL");

const LiquidityWithdrawlAction = (body) => (dispatch) => {
  // alert(body);
  dispatch(LiquidityWithdrawlRequest());
  return new Promise((resolve, reject) => {
    return Api.LiquidityWithdrawl.LiquidityWithdrawlApi(body)
      .then(({ data }) => {
        dispatch(LiquidityWithdrawlSuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(LiquidityWithdrawlFail(error.data));
        reject(error.data);
      });
  });
};
export default LiquidityWithdrawlAction;
