import { createAction, createActions } from "redux-actions";
import Api from "../../Api";

export const LiquidityProvidersHistoryRequest = createAction(
  "LiquidityProvidersHistory_REQUEST"
);
export const LiquidityProvidersHistorySuccess = createAction(
  "LiquidityProvidersHistory_SUCCESS"
);
export const LiquidityProvidersHistoryFail = createAction(
  "LiquidityProvidersHistory_FAIL"
);

const LiquidityProvidersHistoryAction = () => (dispatch) => {
  // alert(body);
  dispatch(LiquidityProvidersHistoryRequest());
  return new Promise((resolve) => {
    return Api.LiquidityProvidersHistory.LiquidityProvidersHistoryApi()
      .then(({ data }) => {
        dispatch(LiquidityProvidersHistorySuccess(data));
        resolve(data);
      })
      .catch((error) => {
        dispatch(LiquidityProvidersHistoryFail(error));
        resolve();
      });
  });
};
export default LiquidityProvidersHistoryAction;
