import React, { useEffect, useState, useRef, Component } from "react";

import { Link, useHistory } from "react-router-dom";
import { PropagateLoader, RingLoader } from "react-spinners";

import { useDispatch, useSelector } from "react-redux";
import { toast, ToastContainer } from "react-toastify";
import GetFavouriteNftAction from "../../../Redux/Actions/NftActions/GetFavouriteNftAction";
import axios from "axios";
import http from "../../../Redux/Api/http";
import RemoveFavouriteNftAction from "../../../Redux/Actions/NftActions/RemoveFavouriteNftAction";
import swal from "sweetalert";
import defaultImg from "../../../assets/images/default.png";
const httpUrl = process.env.REACT_APP_DEVELOPMENT_URL;


class CustomSlide extends Component {
  render() {
    const { index, ...props } = this.props;
    return <div {...props}></div>;
  }
}

function AllFavourite() {
  const history = useHistory();
  const [isloading, setIsloading] = useState(true);
  const [favouriteInProgress, setFavouriteInProgress] = useState(false);

  const dispatch = useDispatch();
  const searchRef = useRef();
  const [allData, setAllData] = useState([]);
  const [filterData, setFilterData] = useState([]);
  const [filterTrigger, setFilterTrigger] = useState(false);

  const [filter, setfilter] = useState([]);
  const WalletAddress = useSelector(
    (state) => state.WalletConnction?.WalletResponse?.accounts
  );

  const Token = useSelector((state) => state.Login?.authResponse?.data?.token);

  const GetFavouriteNft = useSelector(
    (state) => state.GetFavouriteNft?.GetFavouriteNftResponse?.data
  );

  const isConnected = useSelector((state) => state.Login?.authResponse?.data);

  const [favourites, SetFavourites] = useState(GetFavouriteNft?.slice(0, 8));
  const [height, Setheight] = useState(270);

  useEffect(() => {
    SetFavourites(GetFavouriteNft?.slice(0, 8));
    setAllData(GetFavouriteNft);
  }, [GetFavouriteNft]);

  useEffect(async () => {
    await dispatch(GetFavouriteNftAction())
      .then((res) => {
        setIsloading(false);
      })
      .catch((error) => {
        setIsloading(false);
        toast.success(`${error?.message}`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  }, []);

  const loadMore = () => {
    let nftState = favourites;
    let start = nftState?.length;
    let end = nftState?.length + 8;

    if (filterData?.length) {
      SetFavourites([...nftState, ...filterData?.slice(start, end)]);
    } else {
      SetFavourites([...nftState, ...GetFavouriteNft?.slice(start, end)]);
    }
  };

  const onImgLoad = ({ target: img }) => {
    let currentHeight = height;
    if (currentHeight < img.offsetHeight) {
      Setheight({
        height: img?.offsetHeight,
      });
    }
  };

  const handleSearchChange = (e) => {
    const { value } = e.target;

    setfilter(
      allData?.filter((item) =>
        item.name.toLowerCase().includes(value.toLowerCase())
      )
    );
  };

  const resetFilter = () => {
    SetFavourites(allData?.slice(0, 8));
    setfilter([]);
    setFilterTrigger(false);

    searchRef.current.value = "";
  };

  const handlerSearchSubmit = (e) => {
    e.preventDefault();
    setFilterTrigger(true);

    SetFavourites(filter?.slice(0, 8));
    setFilterData(filter);
  };

  const addToFavourite = async (nftID, OwnerAddress) => {
    if (!isConnected) {
      toast.error(`Please connect to wallet first`, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return;
    }
    // console.log("user token ", Token)
    await axios
      .post(
        httpUrl + "/api/v1/Nft/AddFavouriteNft",
        {
          nftId: nftID,
          nftAddress: OwnerAddress,
        },
        {
          headers: {
            Authorization: `Bearer ${Token}`,
          },
        }
      )
      .then(async (resp) => {
        if (resp?.data?.isSuccess === true) {

          const updateFavouriteNumber = favourites.map((item) => {
            if (nftID == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount + 1,

              };
            } else {
              return {
                ...item,
              };
            }
          });

          SetFavourites([...updateFavouriteNumber]);
          toast.success(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });

          await dispatch(GetFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`NFT Already Liked`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        swal({
          icon: "error",
          title: error?.data?.message,
        });
      });
  };

  const removeToFavourite = async (nftId, OwnerAddress) => {
    const payload = {
      nftId: nftId,
      nftAddress: OwnerAddress,
    };

    await dispatch(RemoveFavouriteNftAction(payload))
      .then(async (resp) => {
        if (resp?.isSuccess === true) {
          const updateFavouriteNumber = favourites.map((item) => {
            if (nftId == item.id) {
              return {
                ...item,
                nftFavouritesCount: item.nftFavouritesCount - 1,

              };
            } else {
              return {
                ...item,
              };
            }
          });

          SetFavourites([...updateFavouriteNumber]);
          
          toast.error(`${resp?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          await dispatch(GetFavouriteNftAction());
          // setTimeout(() => window.location.reload(), 2000);
        } else if (resp?.data?.isSuccess === false) {
          toast.error(`${resp?.data?.data}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        swal({
          icon: "error",
          title: error?.data?.message,
        });
      });
  };

  return (
    <>
      <div className="row">
        <div className="col-lg-8 offset-lg-2 col-md-8 offset-md-2 col-sm-12">
          <div className="items_filter w-100">
            <form
              className="row form-dark w-100"
              id="form_quick_search"
              name="form_quick_search"
              onReset={() => {
                resetFilter();
              }}
              onSubmit={handlerSearchSubmit}
            >
              <div className="col-sm-12 d-flex align-items-start justify-content-center p-0">
                <input
                  className="form-control"
                  id="name_1"
                  name="name_1"
                  ref={searchRef}
                  placeholder="search item here..."
                  type="text"
                  onChange={(e) => handleSearchChange(e)}
                  style={{ width: "100%" }}
                />
                <button id="btn-submit">
                  <i className="fa fa-search bg-color-secondary"></i>
                </button>
                {/* <div> */}
                {filterTrigger && (
                  <button id="btn-submit" type="reset">
                    <i class="fas fa-sync bg-danger m-l-1"></i>
                  </button>
                )}
                {/* </div> */}
                <div className="clearfix"></div>
              </div>
            </form>
            {/* <div className="dropdownSelect one">
                <Select
                  className="select1"
                  styles={customStyles}
                  menuContainerStyle={{ zIndex: 999 }}
                  defaultValue={options[0]}
                  options={options}
                />
              </div>
              <div className="dropdownSelect two">
                <Select
                  className="select1"
                  styles={customStyles}
                  defaultValue={options1[0]}
                  options={options1}
                />
              </div>
              <div className="dropdownSelect three">
                <Select
                  className="select1"
                  styles={customStyles}
                  defaultValue={options2[0]}
                  options={options2}
                />
              </div> */}
          </div>
        </div>

      </div>
      <div className="row">
        <ToastContainer
          position="top-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover={false}
        />
        {isloading ? (
          <>
            <div className="col-sm-12 d-flex justify-content-center">
              <RingLoader color="white" size="60" />
            </div>
          </>
        ) : (
          <>
            {favourites?.length == 0 ? (
              <div className="col-sm-12 text-center">No NFT Record Found</div>
            ) : (
              ""
            )}

            {favourites?.map((item, index) => (
              <>
                {console.log("My Favourite items", item)}
                <div
                  key={index}
                  className="d-item col-lg-3 col-md-6 col-sm-6 col-xs-12 mb-4"
                >
                  <div className="nft market-nft">
                    <CustomSlide className="itm" index={1}>
                      <div className="nft_coll">
                        <div className="nft_wrap">
                          <span
                            className=" pic-demo"
                            onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.userFvrtAccountId,
                                
                              })
                            }
                            // onClick={() => {
                            //   history.push(`/usernftdetail/${item?.id}/${item?.userFvrtAccountId}`);
                            // }}
                            style={{
                              background: `url(${httpUrl +
                                "/" +
                                item?.image?.replaceAll("\\", "/")
                                }) no-repeat`,
                            }}
                          >
                            <img
                              src={httpUrl + "/" + item?.image}
                              className="lazy img-more-collection"
                              alt=""
                            />
                          </span>
                        </div>

                        <div className="nft_coll_pp">
                          <span
                            onClick={() => {
                              history.push(
                                item.ownerAddress === WalletAddress
                                  ? `/myprofile`
                                  : `/profile/${item.userFvrtAddress}`
                              );
                            }}
                          >
                            <img
                              className="lazy"
                              src={
                                item?.ownerImage
                                  ? httpUrl + "/" + item?.userProfileImageInFavrt
                                  : defaultImg
                              }
                              alt=""
                            />
                          </span>
                        </div>

                        <div className="nft_coll_info" style={{padding:"30px"}} >
                          <span
                             onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.userFvrtAccountId,
                                
                              })
                            }
                          >
                            {item?.name?.substring().length > 20 ? (
                              <h4> {`${item?.name.substring(0, 20)}...`}</h4>
                            ) : (
                              <>
                                <h4>{item?.name}</h4>
                              </>
                            )
                            }
                          </span>
                          <span
                            onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.userFvrtAccountId,
                                
                              })
                            }
                          >
                            Price {" " +item?.sellPrice? item?.sellPrice: item?.buyPrice + " "}BNB
                          </span>
                          <div class="full-div">
                            <a
                             onClick={() =>
                              history.push({
                                pathname: `/usernftdetail/${item.id}`,
                                state: item.userFvrtAccountId,
                                
                              })
                            }
                              class="view-all-btn"
                            >
                              Detail{" "}
                              <i
                                class="fa fa-angle-right"
                                aria-hidden="true"
                              ></i>
                            </a>

                            {/* <i
                                          onClick={() =>
                                            addToFavourite(
                                              nft?.id,
                                              nft.ownerAddress
                                            )
                                          }
                                          className="fa fa-heart"
                                        ></i> */}
                            {GetFavouriteNft?.some(
                              (itm, index) => {
                                return (
                                  itm.nftTokenId == item.nftTokenId
                                );
                              }
                            ) ? (
                              <i
                                onClick={() => {
                                  setFavouriteInProgress(true);

                                  removeToFavourite(
                                    item.id,
                                    item.ownerAddress,
                                  );
                                }}
                                className="fa fa-heart"
                                style={{ color: "red" }}
                              >
                                {" "}
                                {item?.nftFavouritesCount}
                              </i>
                            ) : (
                              <i
                                onClick={() => {
                                  setFavouriteInProgress(true);
                                  addToFavourite(
                                    item?.id,
                                    item.ownerAddress,
                                  );
                                }}
                                className="fa fa-heart"
                              >
                                {" "}
                                {item?.nftFavouritesCount}
                              </i>
                            )}
                          </div>
                        </div>
                      </div>
                    </CustomSlide>
                  </div>
                </div>

              </>
            ))}

            {filterData?.length && filterTrigger ? (
              <>
                {favourites?.length < filterData?.length && (
                  <div className="col-lg-12" style={{ textAlign: "center" }}>
                    <div className="spacer-single"></div>
                    <span onClick={loadMore} className="btn-main lead m-auto">
                      Load More
                    </span>
                  </div>
                )}
              </>
            ) : (
              <>
                {favourites?.length < GetFavouriteNft?.length &&
                  !filterTrigger && (
                    <div className="col-lg-12" style={{ textAlign: "center" }}>
                      <div className="spacer-single"></div>
                      <span onClick={loadMore} className="btn-main lead m-auto">
                        Load More
                      </span>
                    </div>
                  )}
              </>
            )}
          </>
        )}
      </div>
    </>
  );
}

export default AllFavourite;
